import React from "react";

import { withAuthorization } from "../Session";
import * as ROLES from "../../constants/roles";

import { Grid, Button } from "@material-ui/core";

import CreateTable from "./TableEx";


let userCount;


  class UserList extends ( React.Component) {
  _isMounted = false;

  constructor(props) {
    super(props);
    this.props = props;
    this.state = {
      totalAppUsers: (
        <Button color="secondary" size="small" variant="outlined">
          0
        </Button>
      ),
    };

    if (this._isMounted) {
      this.setState({
        totalAppUsers: (
          <Button color="secondary" size="small">
            {userCount}
          </Button>
        ),
      });
    }
  }

  componentDidMount() {
    this._isMounted = true;
  }

  componentWillUnmount() {
    this._isMounted = false;
  }

  render() {
    

    return (
      <React.Fragment>
        <Grid item xs={12}>
          {/* <Paper className={fixedHeightPaper}> */}
          <CreateTable TableHeader="Manuring of Oil Palm Post-Assesment Questions" />
          {/* </Paper> */}
        </Grid>
      </React.Fragment>
    );
  }
}

// const condition = (authUser) => authUser;
const condition = (authUser) => authUser && (authUser.roles.includes(ROLES.ADMIN) || authUser.roles.includes(ROLES.SUPERADMIN));
export default withAuthorization(condition)(UserList);
// export default (UserList);