import React, { Component } from "react";

import { PasswordForgetLink } from "../PasswordForget";
import * as ROUTES from "../../constants/routes";
import Avatar from "@material-ui/core/Avatar";
import Button from "@material-ui/core/Button";
import CssBaseline from "@material-ui/core/CssBaseline";
import TextField from "@material-ui/core/TextField";
import Paper from "@material-ui/core/Paper";
import Box from "@material-ui/core/Box";
import Grid from "@material-ui/core/Grid";
import LockOutlinedIcon from "@material-ui/icons/LockOutlined";
import Typography from "@material-ui/core/Typography";
import Copyright from "../Common/Copyright";
import Snackbar from "../Common/Snackbar";
import { useStyles } from "../Common/Styles";
import { DoLogin, TokenTest } from "../../helpers/UserHelper";
import authStateChange from "../Session/stateManagement";


function SignIn(props) {
  const classes = useStyles();

  return (
    <div>
      {
        <SignInUI
          value={classes}
          history={props.history}
        />
      }
    </div>
  );
}


const INITIAL_STATE = {
  email: "",
  password: "",
  error: null,

  passwordHidden: "",
  showPassword: false,
  isSubmitted: false,
};


let historyProp = null;

class SignInUI extends Component {
  constructor(props) {
    super(props);
    this.state = { ...INITIAL_STATE };
    this.testfunc = this.testfunc.bind(this);
    // this.DoLogin = this.DoLogin.bind(this);
    historyProp = this.props.history;
  }

  GotoLocation(location) {
    historyProp.push(location);
  }

  IsSubmitHandler(val) {
    this.setState({ isSubmitted: val });
  }

  onChange = (event) => {
    var errorName = [event.target.name] + "Form";
    this.setState({
      [event.target.name]: event.target.value,
      [errorName]: "",
    });
  };

  onSubmit = (event) => {

    event.preventDefault();
    this.IsSubmitHandler(true);


    const { email, password } = this.state;
    let payload = {
      email: email,
      password: password
    };
    DoLogin(payload).then(response => {
      // alert(response);

      if (response) {
        // alert(response.message+ " " + response.status);
        if (response.status === 200) {
          // alert("done");
          let authUser = {
            uid: response.userData._id,
            roles: response.userData.roles,
            username: response.userData.username,
            email: response.userData.email,
          }

          this.setState({ error: { message: "Login Successful!" } });
          // console.log("acc "+response.accessToken+ " ref : "+response.refreshToken);

          // console.log("login response found");
          // {<authStateChange authUser={authUser} next={this.GotoLocation(ROUTES.HOME)} fallback={this.GotoLocation(ROUTES.SIGN_IN)}/>}
          authStateChange(authUser, response.accessToken, response.refreshToken, (authUser) => {
            this.GotoLocation(ROUTES.HOME);
          }, () => {
            this.GotoLocation(ROUTES.SIGN_IN);
          });
          // localStorage.setItem("authUser", authUser);
          // this.props.history.push(ROUTES.HOME);
          // authStateChange(authUser, this.GotoLocation, this.GotoLocation);
        }
        else if (response.status === 406) {
          // alert(JSON.stringify(response) + " asdasddas " + response.data.message);
          this.setState({ error: { message: (response.data.message === undefined ? "Invalid Credentials" : response.data.message) }, email: "", password: "" });
          this.IsSubmitHandler(false);
        }
        else {
          // alert(response.message);
          // alert("fail");
          this.IsSubmitHandler(false);
          this.setState({ error: { message: "Oops something went wrong" }, email: "", password: "" });
        }
      }
      else {
        // alert("fail");
        this.IsSubmitHandler(false);

        this.setState({ error: { message: "Oops something went wrong" }, email: "", password: "" });
      }

    });


  };
  handleCloseAlert = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    this.setState({
      error: null,
    });
  };

  testfunc() {
    // console.log("testfunc");
    TokenTest();
  }

  render() {
    const { email, password } = this.state;

    const isInvalid = password === "" || email === "";
    const classes = this.props.value;
    var validEmailRegEx = /^[A-Z0-9_'%=+!`#~$*?^{}&|-]+([.][A-Z0-9_'%=+!`#~$*?^{}&|-]+)*@[A-Z0-9-]+(\.[A-Z0-9-]+)+$/i;
    var isInvalidEmail = email === "" ? true : validEmailRegEx.test(email);
    var eHelper = !isInvalidEmail ? "Email Address Invalid" : "";

    return (
      <Grid container component="main" className={classes.rootSignIn}>
        <CssBaseline />
        <Grid item xs={false} sm={4} md={7} className={classes.bgSignIn} />
        <Grid item xs={12} sm={8} md={5} component={Paper} elevation={6} square>
          <div className={classes.paperSignIn}>
            <Grid container direction="row" justify="center" spacing={5}>
              {/* <Grid item xs={6}> */}

              <div className={classes.image1} ></div>
              {/* <div>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</div> */}
              {/* </Grid> */}
              {/* <Grid item xs={6}> */}
              <div className={classes.image2}></div>
              {/* </Grid> */}
            </Grid>
            <Avatar className={classes.avatarSignIn}>
              <LockOutlinedIcon />
            </Avatar>
            <Typography component="h1" variant="h5">
              Sign in
            </Typography>
            <form onSubmit={this.onSubmit} className={classes.formSignIn}>
              <TextField
                error={!isInvalidEmail}
                helperText={eHelper}
                name="email"
                value={email}
                onChange={this.onChange}
                type="text"
                placeholder="Email Address"
                variant="outlined"
                margin="normal"
                required
                fullWidth
                id="email"
                label="Email Address"
                autoComplete="email"
                autoFocus
              />
              <TextField
                name="password"
                value={password}
                onChange={this.onChange}
                type="password"
                placeholder="Password"
                variant="outlined"
                margin="normal"
                required
                fullWidth
                label="Password"
                id="password"
                autoComplete="current-password"
              />
              <Button
                type="submit"
                fullWidth
                variant="contained"
                color="primary"
                className={classes.submitSignIn}
                disabled={isInvalid || this.state.isSubmitted}
              >
                Sign In
              </Button>
              {/* <button type="button" onClick={this.testfunc}>test</button> */}
              {/* <SignInGoogle value={classes} /> */}
              <Grid container>
                <Grid item xs>
                  <PasswordForgetLink />

                  {/* <br />
                  <SignUpForm /> */}
                </Grid>
              </Grid>
              <Box mt={5}>
                <Copyright />
              </Box>
            </form>
            {/* <Button variant="outlined" onClick={() => { this.setState({ error: { message: "Test" } }) }}>
              Show Snackbar
            </Button> */}
            {this.state.error && (
              <Snackbar
                severity="error"
                message={this.state.error.message}
                onClose={() => {
                  this.setState({ error: null });
                }}
              />
            )}
          </div>
        </Grid>
      </Grid>
    );
  }
}

const SignInWithUI = (SignIn);

export default (SignInWithUI);