import React, { useEffect } from "react";
import Card from "@material-ui/core/Card";
import CardActionArea from "@material-ui/core/CardActionArea";
import Avatar from "@material-ui/core/Avatar";
import { useStyles } from "../Common/Styles";
// import { withFirebase } from "../Firebase";

// const capitalize = (s) => {
//   if (typeof s !== 'string') return ''
//   return s.charAt(0).toUpperCase() + s.slice(1)
// }

function ImgMedia(props) {
  const classes = useStyles();
  var userName = props.user;
  // var drawerOpen = props.open;
  const [open, setOpen] = React.useState(true);

  useEffect(() => {
    setOpen(props.open);
  }, [props.open]);

  return (
    <Card className={classes.rootUserCard}>
      <CardActionArea style={{}}>
        <center>
          <Avatar className={classes.rootUserAvatar} src="">
            {userName === "" ? "" :userName.charAt(0).toUpperCase()}
          </Avatar>
          {open === true ? <h2>{userName}</h2> : null}
        </center>
      </CardActionArea>
    </Card>
  );
}

// const ImgMediaCard = withFirebase(ImgMedia);
const ImgMediaCard = (ImgMedia);
export default ImgMediaCard;
