import React from "react";
import clsx from "clsx";
import UserList from "./UserList";
import { useStyles } from '../Common/Styles';
import { withAuthorization } from "../Session";
import * as ROLES from "../../constants/roles";


function PostAssesmentQuestions() {
  const classes = useStyles();
  const fixedHeightPaperLarge = clsx(classes.paper, classes.fixedHeightExtraLarge);
  return (
    <UserList
      styleClass={classes}
      styleFixedHeight={fixedHeightPaperLarge}
    />
  );
}

const condition = (authUser) => authUser && (authUser.roles.includes(ROLES.ADMIN) || authUser.roles.includes(ROLES.SUPERADMIN));
 export default withAuthorization(condition)(PostAssesmentQuestions);
