import React from "react";
import { withRouter } from "react-router-dom";
import { compose } from "recompose";

import AuthUserContext from "./context";

import * as ROUTES from "../../constants/routes";
import * as StateManagement from "../Session/stateManagement";

const withAuthorization = (condition) => (Component) => {
  class WithAuthorization extends React.Component {


    componentDidMount() {
      var userData = JSON.parse(localStorage.getItem("authUser"));
      if (userData) {
        if (!condition(userData.authUser)) {
          this.props.history.push(ROUTES.SIGN_IN);
        }
        if (userData != null) {
          StateManagement.CheckForTimeOut(userData.time);
        }
      }
      else {
        let pathName = window.location.pathname.split("&");
        if (pathName[0] === ROUTES.PASSWORD_CHANGE) {
          localStorage.setItem("resetPasswordToken", pathName[1]);
          this.props.history.push(ROUTES.PASSWORD_CHANGE);
        }
        else {
          localStorage.removeItem("authUser");
          this.props.history.push(ROUTES.SIGN_IN);
        }
      }

    }

    componentWillUnmount() {

    }

    render() {
      return (
        <AuthUserContext.Consumer>
          {(authUser) =>
            condition(authUser) ? <Component {...this.props} /> : null
          }
        </AuthUserContext.Consumer>
      );
    }
  }

  return compose(withRouter)(WithAuthorization);
};

export default withAuthorization;
