import React from 'react';
import Button from "@material-ui/core/Button";
import authStateChange from "../Session/stateManagement";
import { LogoutWebUserKeycloak } from "../../helpers/UserHelper";

const SignOutButton = ({ errorHandler }) => (

  <Button variant="contained" type="button" color="secondary" onClick={() => DoSignOut()}>
    Sign Out
  </Button>
);

export function SignOutHelper() {
  localStorage.clear();
  authStateChange(null, null, null, () => {
    ;
  }, () => {
    ;
  });
}

export const DoSignOut = () => {
  // alert("Signing out");
  LogoutWebUserKeycloak().then(response => {
    localStorage.clear();
    authStateChange(null, null, null, () => {
      ;
    }, () => {
      ;
    });
  });



};

