import React, { useEffect, useState } from "react";

import clsx from "clsx";
import { makeStyles, fade } from "@material-ui/core/styles";
import {
  CssBaseline,
  Drawer,
  Box,
  AppBar,
  Toolbar,
  List,
  Typography,
  Divider,
  IconButton,
  Grid,
  Breadcrumbs,
  Link,

} from "@material-ui/core";
import {
  NavigateNext,
  ChevronLeft,
  
} from "@material-ui/icons";
import MenuIcon from "@material-ui/icons/Menu";
import { AuthUserContext, withAuthorization, withAuthentication } from "../Session";
import { AdminListItems, UserListItems, NavBarList } from "../Common/listItems";
import Copyright from "../Common/Copyright";
import SignOutButton from "../SignOut";
import * as ROLES from "../../constants/roles";
import * as ROUTES from "../../constants/routes";
//import { useStyles } from "../Common/Styles";
import ImgMediaCard from "./UserCard";
import { Redirect } from "react-router-dom";
import Snackbar from "../Common/Snackbar";
import { compose } from "recompose";


//import theme from "../Common/theme";

const drawerWidth = 240;

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
  },
  appBar: {
    zIndex: theme.zIndex.drawer + 1,
    transition: theme.transitions.create(["width", "margin"], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },
  appBarShift: {
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(["width", "margin"], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  menuButton: {
    marginRight: 36,
  },
  hide: {
    display: "none",
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
    whiteSpace: "nowrap",
  },
  open: {
    width: drawerWidth,
    transition: theme.transitions.create("width", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
    backgroundColor: theme.palette.primary.main,
  },
  drawerClose: {
    transition: theme.transitions.create("width", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    overflowX: "hidden",
    width: theme.spacing(7) + 1,
    [theme.breakpoints.up("sm")]: {
      width: theme.spacing(9) + 1,
    },
    backgroundColor: theme.palette.primary.main,
  },
  toolbar: {
    display: "flex",
    alignItems: "center",
    justifyContent: "flex-end",
    padding: theme.spacing(0, 1),
    // necessary for content to be below app bar
    ...theme.mixins.toolbar,
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing(3),
  },
  title: {
    flexGrow: 1,
  },
  search: {
    position: "relative",
    borderRadius: theme.shape.borderRadius,
    backgroundColor: fade(theme.palette.common.white, 0.15),
    "&:hover": {
      backgroundColor: fade(theme.palette.common.white, 0.25),
    },
    marginLeft: 0,
    marginRight: theme.spacing(2),
    width: "100%",
    [theme.breakpoints.up("sm")]: {
      marginLeft: theme.spacing(1),
      width: "auto",
    },
  },
  searchIcon: {
    padding: theme.spacing(0, 2),
    height: "100%",
    position: "absolute",
    pointerEvents: "none",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
  },
  inputRoot: {
    color: "inherit",
  },
  inputInput: {
    padding: theme.spacing(1, 1, 1, 0),
    // vertical padding + font size from searchIcon
    paddingLeft: `calc(1em + ${theme.spacing(4)}px)`,
    transition: theme.transitions.create("width"),
    width: "100%",
    [theme.breakpoints.up("sm")]: {
      width: "12ch",
      "&:focus": {
        width: "20ch",
      },
    },
  },
}));

const Dashboard = (props) => (
  <React.Fragment>
    <AuthUserContext.Consumer>
      {(authUser) =>
        // console.log("dashboard reached" + JSON.stringify(authUser))
        authUser.roles != null || authUser.roles === "undefined" ? (
          authUser.roles.includes(ROLES.SUPERADMIN) ? (

            <React.Fragment>
              <DashboardView
                list={AdminListItems}
                type="superadmin"
                children={props.children}
                authUser={authUser}
                nameUpdate={props.name}
              />


            </React.Fragment>
          ) : authUser.roles.includes(ROLES.ADMIN) ? (
            <React.Fragment>
              <DashboardView
                list={AdminListItems}
                type="admin"
                children={props.children}
                authUser={authUser}
                nameUpdate={props.name}
              />


            </React.Fragment>
          ) : (
            <React.Fragment>
              <DashboardView
                list={UserListItems}
                type="user"
                children={props.children}
                authUser={authUser}
                nameUpdate={props.name}
              />
            </React.Fragment>
          )
        ) : (
          <Redirect to={ROUTES.SIGN_IN} />
        )
      }
    </AuthUserContext.Consumer>
  </React.Fragment>
);

function ReadHeader() {
  var retVal = {
    loc: "",
    href: null,
  };
  switch (window.location.pathname) {
    case "/home":
      retVal.loc = "Home";
      retVal.href = ROUTES.HOME;
      break;
    case "/ecocoaabout":
      retVal.loc = "About e-cocoa";
      retVal.href = ROUTES.ECOCOAABOUT;
      break;
    case "/postassesmentquestions":
      retVal.loc = "Post-Assesment Questions";
      retVal.href = ROUTES.POSTASSESMENT;
      break;
    case "/appusers":
      retVal.loc = "e-cocoa Application Users";
      retVal.href = ROUTES.APPUSERS;
      break;
    case "/dashboardviewer":
      retVal.loc = " Dashboard Viewers";
      retVal.href = ROUTES.DASHBOARDVIEWER;
      break;
    case "/admin":
      retVal.loc = "Admin Users";
      retVal.href = ROUTES.ADMIN;
      break;
    case "/changepassword":
      retVal.loc = "Change Password";
      retVal.href = ROUTES.PASSWORD_CHANGE;
      break;
    case "/certificates":
      retVal.loc = "User Certificates";
      retVal.href = ROUTES.CERTIFICATE;
      break;
    default:
      break;
  }
  return retVal;
}

function DashboardView(props) {
  const classes = useStyles();
  //const theme = useTheme();
  const [open, setOpen] = useState(true);
  const [width, setWidth] = useState(window.innerWidth);
  const minScreenSize = 768;

  const thisPage = ReadHeader();

  useEffect(() => {
    const handleWindowResize = () => setWidth(window.innerWidth);
    window.addEventListener("resize", handleWindowResize);
    if (width < minScreenSize) {
      handleDrawerClose();
    }
    return () => window.removeEventListener("resize", handleWindowResize);
  });

  const [errorHandler, SetError] = useState({
    errorType: "",
    errorText: "",
  });

  const handleopen = () => {
    setOpen(true);
  };
  const handleDrawerClose = () => {
    setOpen(false); 
  };

  return (
    //<React.Fragment>
    <div className={classes.root}>
      <CssBaseline />
      <AppBar
        position="fixed"
        className={clsx(classes.appBar, {
          [classes.appBarShift]: open,
        })}
      >
        <Toolbar>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            onClick={handleopen}
            edge="start"
            className={clsx(classes.menuButton, {
              [classes.hide]: open,
            })}
          >
            <MenuIcon />
          </IconButton>
          <Typography
            // component="h1"
            variant="h6"
            color="inherit"
            noWrap
            className={classes.title}
          >
            <Breadcrumbs
              separator={<NavigateNext fontSize="small" />}
              aria-label="breadcrumb"
              style={{ color: "white" }}
            >
              <Link
                color="inherit"
                href="/"
                //onClick={handleClick}
                className={classes.link}
                style={{ color: "white" }}
              >
                Dashboard
              </Link>
              <Typography color="inherit">{thisPage.loc}</Typography>
            </Breadcrumbs>
          </Typography>

          {/* <div className={classes.search}>
            <div className={classes.searchIcon}>
              <Search />
            </div>
            <InputBase
              placeholder="Search…"
              classes={{
                root: classes.inputRoot,
                input: classes.inputInput,
              }}
              inputProps={{ "aria-label": "search" }}
            />
          </div> */}
          <SignOutButton errorHandler={SetError} />
        </Toolbar>
      </AppBar>
      <Drawer
        variant="permanent"
        className={clsx(classes.drawer, {
          [classes.open]: open,
          [classes.drawerClose]: !open,
        })}
        classes={{
          paper: clsx({
            [classes.open]: open,
            [classes.drawerClose]: !open,
          }),
        }}
      >
        <div className={classes.toolbar}>
          <IconButton
            onClick={handleDrawerClose}
          //styles={{ color: theme.palette.primary.contrastText }}
          >
            <ChevronLeft />
          </IconButton>
        </div>
        <ImgMediaCard
          user={
            props.nameUpdate === "" ? props.authUser.username : props.nameUpdate
          }
          open={open}
        />
        <Divider />
        <List className={classes.drawerList}>
          {/* {props.list} */}
          <NavBarList type={props.type} open={open} />
        </List>
        <Divider />
      </Drawer>
      <main className={classes.content}>
        {/* <div className={classes.appBarSpacer} /> */}
        <div className={classes.toolbar} />
        <div style={{ padding: 20 }}>
          <Grid container spacing={3}>
            {props.children}
          </Grid>
        </div>
        {errorHandler.errorText !== "" ? (
          <Snackbar
            severity={errorHandler.errorType}
            message={errorHandler.errorText}
            onClose={() => {
              SetError({ errorText: "" });
            }}
          />
        ) : (
          <div />
        )}
        <Box pt={5}>
          <Copyright />
        </Box>
      </main>
    </div>
    //</React.Fragment >
  );
}

const condition = (authUser) => authUser;
export default compose(withAuthentication, withAuthorization(condition))(Dashboard);
