import React from "react";
import clsx from "clsx";
import UserList from "./UserList";
import { useStyles } from '../Common/Styles';
// import { compose } from "recompose";
// import { withFirebase } from "../Firebase";
import { withAuthorization } from "../Session";
import * as ROLES from "../../constants/roles";

function APPUSERS() {
  const classes = useStyles();
  const fixedHeightPaperSmall = clsx(classes.paper, classes.fixedHeightSmall);
  const fixedHeightPaperLarge = clsx(classes.paper, classes.fixedHeightLarge);

  return (
    <UserList
      styleClass={classes}
      styleFixedHeight={fixedHeightPaperSmall}
      styleFixedHeightLarge={fixedHeightPaperLarge}
    />
  );
}

const condition = (authUser) =>
  authUser &&
  (authUser.roles.includes(ROLES.ADMIN) ||
    authUser.roles.includes(ROLES.SUPERADMIN));
export default withAuthorization(condition)(APPUSERS);


