import { createMuiTheme, responsiveFontSizes } from "@material-ui/core/styles";

const theme = responsiveFontSizes(
  createMuiTheme({
    palette: {
      primary: {
        main: "#002E54",
      },
      //   secondary: {
      //     main: "#ffc72c",
      //   },
    },
    typography: {
      fontFamily: ["Verdana", "Arial", "sans-serif"].join(","),
    },
    overrides: {
      MuiAppBar: {
        colorPrimary: {
          backgroundColor: "#335876",
        },
      },
      MuiListSubheader: {
        root: {
          color: "grey",
        },
      },
    },
  })
);

export default theme;
