import React, { useEffect } from "react";
import certENG from "./NewCertENG.png";
import { Button } from "@material-ui/core";
import { useState } from "react";

const DownloadCert = ({ name, module }) => {
    const [shouldDownload, setShouldDownload] = useState(false);
    const downloadImage = async () => {

        const canvas = document.getElementById("imgCanvas");
        const ctx = canvas.getContext("2d");

        const img = document.getElementById("mainImage");
        img.onload = async () => {
            ctx.drawImage(img, 0, 0);
            ctx.font = "40px Courier Bold";
            ctx.fillStyle = "BLACK";
            ctx.fillText(name, 350, 280); // THIS IS THE PLACE TEXT IS EMBEDDED INTO THE PICTURE
            ctx.fillText(module, 560, 375, 250);

            // add a rectangle to make sure the canvas is being drawn to
            ctx.fillStyle = "red";
            ctx.fillRect(10, 10, 100, 100);

            const b64Code = canvas.toDataURL();
         

            const a = document.createElement("a");
            a.href = b64Code;
            a.download = "cert.png";
            a.click();
        };

        // set the src of the img element after defining its onload callback
        img.src = certENG;
    };

    useEffect(() => {
        if (shouldDownload)
            downloadImage();
    }, [name, module, shouldDownload]);

    return (
        <>
            <div hidden>
                <canvas id="imgCanvas" width={1200} height={1000} />
                <img id="mainImage" />
            </div>
            <Button
                color="primary"
                size="small"
                variant="outlined"
                onClick={async () => {
                    setShouldDownload(true);
                    await downloadImage();
                    setShouldDownload(false);
                }}
            >
                Download
            </Button>
        </>
    );
};

export default DownloadCert;
