import React, { Component } from "react";
import { BrowserRouter as Router, Route, Redirect } from "react-router-dom";
import axios from "axios";

import SignInWithUI from "../SignIn";
import PasswordForgetPage from "../PasswordForget";
import HomePage from "../Home";
import AppUsers from "../AppUsers";
import AdminPage from "../AdminList";
import ChangePasswordWithUI from "../ChangePassword";
import Certificate from "../Certificate";

import * as ROUTES from "../../constants/routes";
import { withAuthentication } from "../Session";
import PostAssesmentQuestions from "../PostAssesmentQuestions";
import EcocoaAbout from "../ecocoaAbout";
import DashboardViewer from "../DashboardViewer";
import Dashboard from "../Dashboard";
import { BASE_URL } from "../../constants/constants";

import theme from "../Common/theme";
import { ThemeProvider } from "@material-ui/core";



