function ReadHeader() {
  var retVal = {
    loc: "",
    href: null,
  };
  // alert("1" + window.location.pathname);
  switch (window.location.pathname) {
    case "/home":
      retVal.loc = "Home";
      retVal.href = ROUTES.HOME;
      break;

    case "/ecocoaabout":
      retVal.loc = "About e-cocoa";
      retVal.href = ROUTES.ECOCOAABOUT;
      break;

    case "/postassesmentquestions":
      retVal.loc = "Post Assesment Questions";
      retVal.href = ROUTES.POSTASSESMENT;
      break;

    case "/appusers":
      retVal.loc = "e-cocoa Application Users";
      retVal.href = ROUTES.APPUSERS;
      break;

    case "/dashboardviewer":
      retVal.loc = " Dashboard Users";
      retVal.href = ROUTES.DASHBOARDVIEWER;
      break;

    case "/admin":
      retVal.loc = "Admin Users";
      retVal.href = ROUTES.ADMIN;
      break;

    case "/certificates":
        retVal.loc = "Certificates";
        retVal.href = ROUTES.CERTIFICATE;
        break;

    // case "/changepassword":
    //   retVal.loc = "Change Password";
    //   retVal.href = ROUTES.PASSWORD_CHANGE;
    //   break;

    // case "/signin":
    //   retVal.loc = "Sign In";
    //   retVal.href = ROUTES.SIGN_IN;
    //   break;

    case "/":
      retVal.loc = "None";
      retVal.href = ROUTES.HOME;
      break;
    default:
      retVal.loc = "None";
      retVal.href = ROUTES.HOME;
      break;
  }
  return retVal;
}

class DirectionComp extends Component {
  render() {
    var header = ReadHeader();
    var dir = header.href;
    // alert("2" + dir);
    return <Redirect to={dir} />;
  }
}

export const NameUpdaterContext = React.createContext(null);

function App() {
  const nameReducer = (name, action) => {
    return action;
  };

  const [nameUpdate, NameUpdater] = React.useReducer(nameReducer, "");
  // let header = ReadHeader();
  // alert(header.href);
  // let loader = header.href === ROUTES.SIGN_IN ? SignInWithUI : ChangePasswordWithUI;

  return (
    <div>
      <ThemeProvider theme={theme}>
        <Router>

          <Route path={ROUTES.PASSWORD_CHANGE} component={ChangePasswordWithUI} />
          <Route path={ROUTES.SIGN_IN} component={SignInWithUI} />
          <Route
            exact
            path={ROUTES.PASSWORD_FORGET}
            component={PasswordForgetPage}
          />

          <NameUpdaterContext.Provider value={NameUpdater}>
            <Dashboard name={nameUpdate} >
              <Route exact path={ROUTES.HOME} component={HomePage} />
              <Route exact path={ROUTES.APPUSERS} component={AppUsers} />
              <Route
                exact
                path={ROUTES.POSTASSESMENT}
                component={PostAssesmentQuestions}
              />
              <Route exact path={ROUTES.CERTIFICATE} component={Certificate} />
              <Route exact path={ROUTES.ADMIN} component={AdminPage} />
              <Route exact path={ROUTES.ECOCOAABOUT} component={EcocoaAbout} />
              <Route
                exact
                path={ROUTES.DASHBOARDVIEWER}
                component={DashboardViewer}
              />
              <DirectionComp />

            </Dashboard>
          </NameUpdaterContext.Provider>

          {/* <Redirect to={ROUTES.SIGN_IN} /> */}
        </Router>
      </ThemeProvider>
    </div>
  );
}

export default withAuthentication(App);
