import { DoSignOut } from "../SignOut";
const userTimeout = 15; // in minutes
const userTimeoutInMiliseconds = userTimeout * 60 * 1000;

export function SetLocalStorage(authUser, authToken, refreshToken) {
  var now = new Date().getTime();
  const userData = {
    time: now,
    authUser: authUser,
  };
  localStorage.setItem("authUser", JSON.stringify(userData));
  localStorage.setItem("auth_token", authToken);
  localStorage.setItem("refreshToken", refreshToken);
}

export function CheckForTimeOut(userTime) {
  var now = new Date().getTime();
  if (now - userTime > userTimeoutInMiliseconds) {
    DoSignOut();
    return true;
  } else {
    return false;
  }
}

export default function authStateChange(authUser, accessToken, refreshToken, next, fallback) {
  if (authUser) {
    SetLocalStorage(authUser, accessToken, refreshToken);
    next();
    window.location.reload();
  } else {
    fallback();
    window.location.reload();
  }
}



