import React from "react";
import MUIDataTable from "mui-datatables";
import { MuiThemeProvider, createMuiTheme } from "@material-ui/core/styles";
import { CircularProgress } from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
// import * as ROLES from "../../constants/roles";
import FirebaseEditModal from "../Common/EditModal";
import ActiveToggleFirebase from "../Common/ActiveToggle";
import DeleteButtonFirebase from "../Common/DeleteButton";
import { GetDashboardViewerList } from "../../helpers/UserHelper";

function createTableData(
  _id,
  email,
  org,
  lastLogin,
  status,
  userName,
  roles,
  errorHandler,
  refreshHandler
) {
  var temp = [];
  temp.push(_id);
  temp.push(userName);
  temp.push(email);
  temp.push(org);
  temp.push(lastLogin);
  var togAct = (
    <ActiveToggleFirebase
      uid={_id}
      status={status}
      org={org}
      em={email}
      uname={userName}
      dbType="web_users/"
      errorHandler={errorHandler}
      refreshHandler={refreshHandler}
    />
  );
  temp.push(togAct);
  var but = (
    <React.Fragment>
      <Grid container>
        <Grid item xs={6}>
          <FirebaseEditModal
            uid={_id}
            uname={userName}
            org={org}
            em={email}
            status={status}
            roles={roles}
            errorHandler={errorHandler}
            refreshHandler={refreshHandler}
          />
        </Grid>
        <Grid item xs={6}>
          <DeleteButtonFirebase
            type="Web"
            uid={_id}
            errorHandler={errorHandler}
            refreshHandler={refreshHandler}
            email={email}
          />
        </Grid>
      </Grid>
    </React.Fragment>
  );
  temp.push(but);
  return temp;
}

//let users = null;

const loadingData = [
  [
    "",
    "Loading Data",
    <CircularProgress />,
    <CircularProgress />,
    <CircularProgress />,
    <CircularProgress />,
    <CircularProgress />,
  ],
];
const noData = [["", "No User Found"]];

const columns = [
  {
    name: "UID",
    options: {
      display: false,
      sort: false,
      filter: false,
    },
  },
  {
    name: "Username",
    options: {
      display: true,
      sort: false,
      filter: false,
    },
  },
  {
    name: "Email",
    options: {
      filter: true,
      sort: true,
    },
  },
  {
    name: "Organization Name",
    options: {
      filter: true,
      sort: true,
    },
  },
  {
    name: "Last Login",
    options: {
      filter: true,
      sort: true,
    },
  },
  {
    name: "Status",
    options: {
      filter: true,
      sort: true,
    },
  },
  {
    name: "Actions",
    options: {
      filter: true,
      sort: true,
    },
  },
];

const options = {
  customToolbarSelect: () => { },
  filterType: "dropdown",
  print: false,
  download: true,
  viewColumns: false,
  selectableRows: false
};

const theme = createMuiTheme({
  overrides: {
    MUIDataTableSelectCell: {
      expandDisabled: {
        // Soft hide the button.
        visibility: "show",
      },
    },
  },
});

class CreateTable extends React.Component {
  constructor(props) {
    super(props);
    this.props = props;
    this.state = {
      tableData: [],
      refresh: false,
      isLoading: false,
    };
    this.errorHandler = this.props.errorHandler;
    this.refreshHandler = this.RefreshHandler.bind(this);
  }

  _isMounted = false;

  RefreshHandler(ref) {
    if (ref) {
      this.setState({
        refresh: false,
        tableData: [],
      });
      this.componentDidMount();
    }
  }

  async componentDidMount() {
    this._isMounted = true;
    let tmpData = [];
    let ll = "";   
    let adminList = [];
    GetDashboardViewerList().then(adminListReceived => {
      adminList = JSON.parse(adminListReceived.data.userList);
      if (this._isMounted) {
        for (var key in adminList) {
          this.setState({ isLoading: true });
          if (adminList[key]["lastLogin"]) {
            // console.log(adminList[key]["lastLogin"]);
            ll = new Date(adminList[key]["lastLogin"]).toDateString();
          }
          tmpData.push(
            createTableData(
              adminList[key]["_id"],
              adminList[key]["email"],
              adminList[key]["org"],
              ll,
              adminList[key]["isActive"],
              adminList[key]["username"],
              adminList[key]["roles"],
              this.errorHandler,
              this.refreshHandler
            )
          );
        }
        if (tmpData.length > 0) {
          this.setState({
            tableData: tmpData,
          });
        } else {
          this.setState({
            isLoading: false,
          });
        }
      }
    });
  }

  componentWillUnmount() {
    this._isMounted = false;

    this.setState({
      tableData: [],
      isLoading: false,
    });
  }

  render() {
    return (
      <React.Fragment>
        {this.state.tableData.length <= 0 ? (
          <center>
            <MuiThemeProvider theme={theme}>
              <MUIDataTable
                title={this.props.header}
                data={this.state.isLoading ? loadingData : noData}
                columns={columns}
                options={options}
              />
            </MuiThemeProvider>
          </center>
        ) : (
          <MuiThemeProvider theme={theme}>
            <MUIDataTable
              title={this.props.header}
              data={this.state.tableData}
              columns={columns}
              options={options}
            />
          </MuiThemeProvider>
        )}
      </React.Fragment>
    );
  }
}

export default CreateTable;
