import React from "react";
import Modal from "@material-ui/core/Modal";
// import { withFirebase } from "../Firebase";
import { Button, Grid } from "@material-ui/core";
// import Typography from "@material-ui/core/Typography";
import { useStyles } from "./Styles";
import EditIcon from "@material-ui/icons/Edit";
// import { NameUpdaterContext } from "../App";
// import { UpdateWebUser_Name_Org } from "../../helpers/UserHelper";

function getModalStyle() {
  const top = 50;
  const left = 50;

  return {
    top: `${top}%`,
    left: `${left}%`,
    transform: `translate(-${top}%, -${left}%)`,
  };
}

function ViewImage(props) {
  const imageURL = props.imageURL;
  const classes = useStyles();

  const [modalStyle] = React.useState(getModalStyle);
  const [open, setOpen] = React.useState(false);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const body = (
    <div style={modalStyle} className={classes.viewImageModal} >
      <Grid
        container
        direction="row"
        justify="space-between"
        alignItems="center"
        spacing={2}
      >
        <Grid item lg={12} >
          <center>
            <img src={imageURL} height = "250vh"  width = "200vh" alt="questionImage"></img>
          </center>
        </Grid>

      </Grid>
    </div>
  );

  return (
    <div>
      <Button
        variant="outlined"
        size="small"
        type="button"
        color="secondary"
        onClick={handleOpen}
        startIcon={<EditIcon />}
      >
        View Question Image
      </Button>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="simple-modal-title"
        aria-describedby="simple-modal-description"
      >
        {body}
      </Modal>
    </div>
  );
}


export default ViewImage;
