import { BASE_URL } from '../constants/constants';
import { CheckForTimeOut } from "../components/Session/stateManagement";

require('dotenv').config();
const axios = require('axios');

export async function FetchUserAPI() {
    const accessToken = localStorage.getItem("auth_token");
    var userData = JSON.parse(localStorage.getItem("authUser"));
    if (CheckForTimeOut(userData.time)) {
        ;
    }
    else {

        if (accessToken) {

            return await axios.post(BASE_URL + "/fetchusers", {}, {
                headers: {
                    'Authorization': `Bearer ${accessToken}`
                }
            })
                .then(function (response) {
                    // console.log(response);
                    return response;
                })
                .catch(function (error) {
                    // console.log(error);
                });
        }
        else {
            // console.log(" no userdata helper ");
        }
    }
}

