export async function GetUserByProvince(payload) {
    const accessToken = localStorage.getItem('auth_token');
    var userData = JSON.parse(localStorage.getItem("authUser"));
    if (CheckForTimeOut(userData.time)) {
        ;
    }
    else {

        return await axios.post(BASE_URL + "/fetchbyprovince", payload, {
            headers: {
                'Authorization': `Bearer ${accessToken}`
            }
        })
            .then(function (response) {
                //console.log(response);
                return response;
            })
            .catch(function (error) {
                //console.log(error);
            });
    }
}



export async function FetchUserMobileAPI() {
    try {
        const userData = JSON.parse(localStorage.getItem("authUser"));
        const accessToken = localStorage.getItem("auth_token");

        if (CheckForTimeOut(userData?.time)) {
            throw new Error("User session timed out");
        }

        if (!accessToken) {
            throw new Error("Access token not found");
        }

        const response = await axios.post(BASE_URL + "/fetchusersmobile", {}, {
            headers: {
                'Authorization': `Bearer ${accessToken}`,
                'Content-Type': 'application/json'
            }
        })

        console.log(response);
        const userList = JSON.parse(response.data.userList)
        // slice the array to get only 10 users


        return { ...response, userList };
    } catch (error) {
        console.log(error);
        // throw new Error("Failed to fetch user mobile API");
    }
}
