import React from "react";
import Modal from "@material-ui/core/Modal";
import { Grid } from "@material-ui/core";
import IconButton from "@material-ui/core/IconButton";
import Typography from "@material-ui/core/Typography";
import { useStyles } from "../Common/Styles";
import MUIDataTable from "mui-datatables";

import { MuiThemeProvider, createMuiTheme } from "@material-ui/core/styles";
import UnfoldMoreIcon from "@material-ui/icons/UnfoldMore";
import CancelIcon from "@material-ui/icons/Cancel";
import { FetchUserMobileAPI } from "../../helpers/DashboardHelper.js";


var questionPruningJSON = require("../Common/pruning_mod1.json");
var questionFertilizingJSON = require("../Common/fertilizing_mod2.json");


function DatabaseMapCheck(val) {
  if (val === "" || val === "-1" || val === " " || val == null) {
    return false;
  } else {
    return true;
  }
}

// fucntion CheckForNoAnswer(val){
//   if()
// }

function GetOptionVal(val) {
  switch (val) {
    case 0:
      return "A";
    case 1:
      return "B";
    case 2:
      return "C";
    case 3:
      return "D";
    default:
      return "NA";
  }
}

function GetPreQuestionAnswer(qID, aID, module = "0") {
  var mapped = {
    qNo: "",
    q: "",
    a: "",
  };
  var questionjson = module === "0" ? questionPruningJSON : questionFertilizingJSON;
  if (DatabaseMapCheck(qID) && DatabaseMapCheck(aID)) {
    questionjson.questions.map((question) => {
      var id = question.questionID.toString();
      if (id === qID) {
        mapped.q = question.questionText.toString();
        mapped.a = question.options[aID]
          ? question.options[aID].optionText.toString()
          : "NA";
        mapped.qNo = qID;
      }
      return null;
    });
    return mapped;
  } else return null;
}

function GetPostQuestionAnswer(num, qID, aID, module = "0") {
  var mapped = {
    id: null,
    q: "",
    a: "",
  };
  var questionjson = module === "0" ? questionPruningJSON : questionFertilizingJSON;
  if (DatabaseMapCheck(qID) && DatabaseMapCheck(aID)) {
    questionjson.questions.map((question) => {
      var id = question.questionID.toString();
      if (id === qID) {
        mapped.id = num;
        mapped.q = question.questionText.toString();
        mapped.a = GetOptionVal(aID);
      }
      return null;
    });
    return mapped;
  } else {
    return null;
  }
}


function getModalStyle() {
  const top = 50;
  const left = 50;

  return {
    top: `${top}%`,
    left: `${left}%`,
    transform: `translate(-${top}%, -${left}%)`,
  };
}

function CreatePreTableData(ans, ques, quesNo) {
  var temp = [];
  for (var i = 0; i < ans.length; i++) {
    temp.push(createPreData(quesNo[i], ans[i], ques[i]));
  }
  return temp;
}

function createPreData(i, a, q) {
  var temp = [];
  var ques = "";
  ques = "Q" + i + ": " + q;
  temp.push(ques);
  temp.push(a);
  return temp;
}

function CreatePostTableData(uid, po, res) {
  var temp = [];
  var tempt = [];
  var attempt = 0;
  var max = po.length / 10;
  for (var i = 0; i < max; i++) {
    tempt = [];
    tempt.push(i + 1);
    var iteratorMax = 10 + attempt <= po.length ? 10 : po.length - attempt;
    for (var j = 0; j < iteratorMax; j++) {
      tempt.push(po[j + attempt].a);
    }
    tempt.push(res[i]);
    attempt += 10;
    temp.push(tempt);
  }
  return temp;
}

function InfoModal(props) {
  // const c = props.c;
  // const pv = props.pv;
  const landSize = props.ls;
  // const averageProduction = props.ap;
  // const averageHarvest = props.ah;
  // const baseProduction = props.bp;
  // const baseProductionPerLandSize = props.bpl;
  const farmerAssociation = props.fa;
  const farmerGroup = props.fg;

  const uid = props.uid;
  // let errorHandler = props.errorHandler;

  // const dataMain = [[landSize, averageHarvest, averageProduction, baseProduction, baseProductionPerLandSize, farmerAssociation, farmerGroup]];
  const dataMain = [[landSize, farmerAssociation, farmerGroup]];

  const [TableStates, SetTableData] = React.useState({
    pre: [],
    post: [],
    preHar: [],
    postHar: [],
    preFetch: false,
    postFetch: false,
  });

  const classes = useStyles();

  const [modalStyle] = React.useState(getModalStyle);
  const [open, setOpen] = React.useState(false);

  const result = props.result;

  React.useEffect(() => {
    var preQuesNo = [];
    var preA = [];
    var preQ = [];
    var po = [];
    var r = [];

    var preAnsFertilizing = [];
    var preQuesFertilizing = [];
    var preQuesNoFertilizing = [];
    var poHar = [];
    var rHar = [];
    // let listOfUsers = [];
    // FetchUserMobileAPI()
    //   .then(response => {
    //     let result = JSON.parse(response.data.userList);
    // console.log("result " + JSON.stringify(result));
    // listOfUsers = result;
    for (var key in result) {
      if (result[key]["_id"] === uid) {

        var mod = result[key]["moduleResults"]["0"];

        //pre Ass mapping
        for (var preID in mod["preResult"]) {
          var pre = mod["preResult"][preID];
          var mapPreQues = GetPreQuestionAnswer(
            pre["questionID"],
            pre["answer"]
          );
          if (mapPreQues != null) {
            preQ.push(mapPreQues.q);
            preA.push(mapPreQues.a);
            preQuesNo.push(mapPreQues.qNo);
          }
        }

        //post ass mapping
        var pcount = 0;
        //postCount = mod["postResults"].length;
        for (var poID in mod["postResults"]) {
          pcount++;
          for (var poR in mod["postResults"][poID]["postResult"]) {
            var post = mod["postResults"][poID]["postResult"][poR];
            var mapPostQues = GetPostQuestionAnswer(
              pcount,
              post["questionID"],
              post["answer"]
            );
            if (mapPostQues != null) {
              po.push(mapPostQues);
            }
          }
        }

        //result
        var count = 0; // to ignore first result - Bin Wei structure ( result[0] is pre which is not to be stored or display anymore)
        for (var rID in mod["results"]) {
          count++;

          if (count !== 1) {
            r.push(mod["results"][rID]["score"]);
          }
        }

        var tempPre = [];
        tempPre = CreatePreTableData(preA, preQ, preQuesNo);
        var tempPost = [];
        tempPost = CreatePostTableData(uid, po, r);

        //============================================Fertilizing Module==========================================================

        var tempPostHar = [];
        var tempPreHar = [];

        if (result[key]["moduleResults"]["1"] != null) {
          mod = result[key]["moduleResults"]["1"];

          //pre Ass mapping
          for (var preIDFertilizing in mod["preResult"]) {
            pre = mod["preResult"][preIDFertilizing];
            var mapPreQuesFertilizing = GetPreQuestionAnswer(
              pre["questionID"],
              pre["answer"],
              "1"
            );
            if (mapPreQuesFertilizing != null) {
              preQuesFertilizing.push(mapPreQuesFertilizing.q);
              preAnsFertilizing.push(mapPreQuesFertilizing.a);
              preQuesNoFertilizing.push(mapPreQuesFertilizing.qNo);
            }
          }

          //post ass mapping
          pcount = 0;
          for (var poIDFertilizing in mod["postResults"]) {
            pcount++;
            for (var poRFertilizing in mod["postResults"][poIDFertilizing]["postResult"]) {
              var postFertilizing = mod["postResults"][poIDFertilizing]["postResult"][poRFertilizing];
              var mapPostQuesFertilizing = GetPostQuestionAnswer(
                pcount,
                postFertilizing["questionID"],
                postFertilizing["answer"],
                "1"
              );
              if (mapPostQuesFertilizing != null) {
                poHar.push(mapPostQuesFertilizing);
              }
            }
          }

          //result
          count = 0; // to ignore first result - Bin Wei structure ( result[0] is pre which is not to be stored or display anymore)
          for (var rIDFertilizing in mod["results"]) {
            count++;

            if (count !== 1) {
              rHar.push(mod["results"][rIDFertilizing]["score"]);
            }
          }


          tempPreHar = CreatePreTableData(preAnsFertilizing, preQuesFertilizing, preQuesNoFertilizing);
          tempPostHar = CreatePostTableData(uid, poHar, rHar);
        }



        // if (tempPost.length > 0) {
        SetTableData({
          post: tempPost,
          pre: tempPre,
          postHar: tempPostHar,
          preHar: tempPreHar,
          //  postFetch: true,
        });
      }

    }


    // });


    return () => {
      SetTableData({
        post: [],
        pre: [],
      });
    };
  }, [open]); // eslint-disable-line react-hooks/exhaustive-deps

  const columnsMain = [

    {
      name: "Land size (ha)",
      options: {
        filter: true,
        sort: true,
      },
    },
    {
      name: "Farmer Association",
      options: {
        filter: true,
        sort: true,
      },
    },
    {
      name: "Farmer Group",
      options: {
        filter: true,
        sort: true,
      },
    },
  ];

  const columnsSec = [
    {
      name: "Question",
      options: {
        filter: true,
      },
    },
    {
      name: "Answer",
      options: {
        filter: true,
        sort: true,
      },
    },
  ];

  const columnsTer = [
    {
      name: "Attempt",
      options: {
        filter: true,
      },
    },
    {
      name: "Q1",
      options: {
        filter: true,
        sort: true,
      },
    },
    {
      name: "Q2",
      options: {
        filter: true,
        sort: true,
      },
    },
    {
      name: "Q3",
      options: {
        filter: true,
        sort: true,
      },
    },
    {
      name: "Q4",
      options: {
        filter: true,
        sort: true,
      },
    },
    {
      name: "Q5",
      options: {
        filter: true,
        sort: true,
      },
    },
    {
      name: "Q6",
      options: {
        filter: true,
        sort: true,
      },
    },
    {
      name: "Q7",
      options: {
        filter: true,
        sort: true,
      },
    },
    {
      name: "Q8",
      options: {
        filter: true,
        sort: true,
      },
    },
    {
      name: "Q9",
      options: {
        filter: true,
        sort: true,
      },
    },
    {
      name: "Q10",
      options: {
        filter: true,
        sort: true,
      },
    },
    {
      name: "Result",
      options: {
        filter: true,
        sort: true,
      },
    },
  ];

  const options = {
    customToolbarSelect: () => { },
    filter: true,
    filterType: "dropdown",
    // responsive: "scrollMaxHeight",
    // responsive: "stacked",
    print: false,
    viewColumns: false,
    selectableRows: false

  };

  const theme = createMuiTheme({
    overrides: {
      MUIDataTableSelectCell: {
        expandDisabled: {
          visibility: "show",
        },
      },
    },
  });



  const handleOpen = () => {
    // console.log("opening");
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  //const defData = [["Loading", <CircularProgress />]];

  const body = (
    <div style={modalStyle} className={classes.paperModal2}>
      <Grid container spacing={3}>
        <Grid item xs={6}>
          <center>
            <Typography
              component="h2"
              variant="h6"
              color="primary"
              gutterBottom
              align="right"
            >
              User Statistics
            </Typography>
          </center>
        </Grid>
        <Grid item xs={6}>
          <Typography align="right">
            <IconButton
              color="secondary"
              aria-label="close details"
              component="span"
              onClick={handleClose}
            >
              <CancelIcon />
              {/* fontSize="large"
              color="secondary"
              type="button"
              onClick={handleClose} */}
            </IconButton>
          </Typography>
        </Grid>
        <Grid item xs={12}>
          {/* {dataMain.length > 0 ? ( */}
          <MuiThemeProvider theme={theme}>
            <MUIDataTable
              title="User Details"
              data={dataMain}
              columns={columnsMain}
              options={options}
            />
          </MuiThemeProvider>
          {/* ) : (
            <MuiThemeProvider theme={theme}>
              <MUIDataTable
                title="Assessment"
                data={defData}
                columns={columnsMain}
                options={options}
              />
            </MuiThemeProvider>
          )} */}
        </Grid>
        <Grid item xs={12}>
          {/* {TableStates.preFetch === true ? ( */}
          <MuiThemeProvider theme={theme}>
            <MUIDataTable
              title="Pruning of Cocoa Pre-Assessment"
              data={TableStates.pre}
              columns={columnsSec}
              options={options}
            />
          </MuiThemeProvider>
          {/* ) : (
            <MuiThemeProvider theme={theme}>
              <MUIDataTable
                title="Pre-Assessment"
                data={defData}
                columns={columnsSec}
                options={options}
              />
            </MuiThemeProvider>
          )} */}
        </Grid>
        <Grid item xs={12}>
          {/* {TableStates.postFetch === true ? ( */}
          <MuiThemeProvider theme={theme}>
            <MUIDataTable
              title="Pruning of Cocoa Post-Assessment"
              data={TableStates.post}
              columns={columnsTer}
              options={options}
            />
          </MuiThemeProvider>
          {/* ) : (
            <MuiThemeProvider theme={theme}>
              <MUIDataTable
                title="Post-Assessment"
                data={defData}
                columns={columnsTer}
                options={options}
              />
            </MuiThemeProvider>
          )} */}
        </Grid>


        <Grid item xs={12}>
          {/* {TableStates.preFetch === true ? ( */}
          <MuiThemeProvider theme={theme}>
            <MUIDataTable
              title="Fertilizing of Cocoa Pre-Assessment"
              data={TableStates.preHar}
              columns={columnsSec}
              options={options}
            />
          </MuiThemeProvider>
          {/* ) : (
            <MuiThemeProvider theme={theme}>
              <MUIDataTable
                title="Pre-Assessment"
                data={defData}
                columns={columnsSec}
                options={options}
              />
            </MuiThemeProvider>
          )} */}
        </Grid>
        <Grid item xs={12}>
          {/* {TableStates.postFetch === true ? ( */}
          <MuiThemeProvider theme={theme}>
            <MUIDataTable
              title="Fertilizing of Cocoa Post-Assessment"
              data={TableStates.postHar}
              columns={columnsTer}
              options={options}
            />
          </MuiThemeProvider>
          {/* ) : (
            <MuiThemeProvider theme={theme}>
              <MUIDataTable
                title="Post-Assessment"
                data={defData}
                columns={columnsTer}
                options={options}
              />
            </MuiThemeProvider>
          )} */}
        </Grid>


      </Grid>
    </div>
  );

  return (
    <div>
      <MuiThemeProvider theme={theme}>
        <UnfoldMoreIcon fontSize="large" type="button" onClick={handleOpen} />
        <Modal
          open={open}
          onClose={handleClose}
          aria-labelledby="simple-modal-title"
          aria-describedby="simple-modal-description"
        >
          {body}
        </Modal>
      </MuiThemeProvider>
    </div>
  );
}

