import React from "react";
import { withAuthorization } from "../Session";
import * as ROLES from "../../constants/roles";
import {
  Grid,
} from "@material-ui/core";
import CreateTable from "./TableEx";
import Snackbar from "../Common/Snackbar";

class UserList extends (React.Component) {
  constructor(props) {
    super(props);
    this.props = props;
    this.state = {
      errorText: "",
      errorType: "",
    };

    this.errorHandler = this.ErrorHandler.bind(this);
  }
  _isMounted = false;

  ErrorHandler(type, text) {
    this.setState({
      errorText: text,
      errorType: type,
    });
  }

  componentDidMount() {
    this._isMounted = true;
  }

  componentWillUnmount() {
    this._isMounted = false;
  }

  render() {
    const fixedHeightPaperLarge = this.props.styleFixedHeightLarge;

    return (
      <React.Fragment>

        <Grid item xs={12}>
          <CreateTable
            TableHeader="User Certificates"
            paperLarge={fixedHeightPaperLarge}
            errorHandler={this.errorHandler}
          />
        </Grid>

        {this.state.errorText !== "" ? (
          <Snackbar
            severity={this.state.errorType}
            message={this.state.errorText}
            onClose={() => {
              this.setState({ errorText: "" });
            }}
          />
        ) : (
          <div />
        )}
      </React.Fragment>
    );
  }
}

const condition = (authUser) =>
  authUser &&
  (authUser.roles.includes(ROLES.ADMIN) ||
    authUser.roles.includes(ROLES.SUPERADMIN));
export default withAuthorization(condition)(UserList);