import React from "react";
import { Grid, TextField, Paper, Button, Typography } from "@material-ui/core";
import CreateTable from "./TableEx";
import Snackbar from "../Common/Snackbar";
import { CircularProgress } from "@material-ui/core";
import { RegisterWebUserDashboard } from "../../helpers/UserHelper";

import * as ROLES from "../../constants/roles";
import { withAuthorization } from "../Session";
import { compose } from "recompose";

import * as ResetPasswordHelper from "../../helpers/ResetPasswordHelper";


//let users;
//let userCount;
//var viewCount = 0;

const INITIAL_STATE = {
  username: "",
  email: "",
  passwordOne: "",
  passwordTwo: "",
  isActive: "true",
  org: "",
  passwordHidden: "",
  showPassword: false,
};

const crypto = window.crypto || window.msCrypto;

function CreatePassword() {
  let length = 14;
  let wishList = "0123456789ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz~!@-#$";
  var pass = Array.from(crypto.getRandomValues(new Uint32Array(length)))
    .map((x) => wishList[x % wishList.length])
    .join("");
  return pass;
}

class UserList extends (React.Component) {
  constructor(props) {
    super(props);
    this.props = props;
    this.state = {
      ...INITIAL_STATE,
      totalAppUsers: (
        <Button color="secondary" size="small" variant="outlined">
          0
        </Button>
      ),
      formErrorState: "",
      emailForm: "",
      passwordOneForm: "",
      orgForm: "",
      passwordTwoForm: "",
      usernameForm: "",
      tableData: [],
      errorText: "",
      errorType: "",
      refresh: false,
      isSubmitted: false,
    };
    //viewCount = 0;
    this.errorHandler = this.ErrorHandler.bind(this);
  }
  _isMounted = false;

  ErrorHandler(type, text) {
    this.setState({
      errorText: text,
      errorType: type,
    });
  }

  RefreshCall() {
    var self = this;
    setTimeout(function () {
      self.setState({
        refresh: false,
      });
    }, 1000);
  }

  IsSubmitWorking(val) {
    this.setState({
      isSubmitted: val,
    });
  }

  async componentDidMount() {
    this._isMounted = true;
    // this.props.firebase.db
    //   .ref("/web_users/")
    //   .once("value")
    //   .then(function (snapshot) {
    //     //users = snapshot.val();
    //     for (var key in snapshot.val()) {
    //       if (ROLES.ADMIN.localeCompare(snapshot.val()[key]["roles"]) === 0) {
    //         viewCount = viewCount + 1;
    //       }
    //     }
    //     // self.setState({
    //     //   totalAppUsers: (
    //     //     <Button color="secondary" size="small">
    //     //       {viewCount}
    //     //     </Button>
    //     //   ),
    //     // });
    //   });
  }

  componentWillUnmount() {
    this._isMounted = false;
  }

  onSubmit = async (event) => {
    event.preventDefault();
    const {
      username,
      email,
      org,
    } = this.state;

    let isActive = "true";
    var self = this;

    var validEmailRegEx = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])+([.][a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?)*$/i;
    var isValidEmail = email === "" ? true : validEmailRegEx.test(email);
    var eHelper = !isValidEmail ? "The Email format is invalid." : "";

    var usernameRegEx = /^[^\s][a-zA-Z0-9\s]+$/i;
    var isValidUsername = username === "" ? true : usernameRegEx.test(username);
    var uHelper = !isValidUsername
      ? "The Username you entered is invalid."
      : "";

    const formError =
      email === "" ||
      org === "" ||
      username === "" ||
      !isValidUsername ||
      !isValidEmail;

    this.setState({
      formErrorState: formError ? "Please fill in this field." : "",
      usernameForm:
        uHelper === ""
          ? username === ""
            ? "Please enter a Username."
            : ""
          : "The Username you entered is invalid.",
      emailForm:
        eHelper === ""
          ? email === ""
            ? "Please enter an Email."
            : ""
          : "The Email format is invalid.",
      orgForm: org === "" ? "Please enter an Organization." : "",
    });

    if (formError === false) {
      this.IsSubmitWorking(true);
      let password = "";
      password = CreatePassword();
      // password = "1234";
      // console.log(password);
      let payload = {
        "email": email,
        "isActive": isActive,
        "password": password,
        "org": org,
        "roles": ["ADMIN"],
        "username": username
      };

      // console.log(payload);

      RegisterWebUserDashboard(payload).then(response => {
        if (response.data.success) {
          self.ErrorHandler("success", response.data.message);
          self.setState({
            username: "",
            email: "",
            passwordOne: "",
            passwordTwo: "",
            isActive: "true",
            org: "",
            refresh: true,
          });
          let payloadNext = {
            email: email
          };
          ResetPasswordHelper.SendResetPasswordMail(payloadNext).then(response => {
            if (response) {
              if (response.status === 200) {
                self.ErrorHandler("success", "Email with login details sent!");
              }
              else {
                self.ErrorHandler("error",response.data.message);

              }
            }
          });
          self.RefreshCall();
          self.IsSubmitWorking(false);
        }
        else {
          self.ErrorHandler("error", response.data.message);
          self.IsSubmitWorking(false);
        }
      });
    } else {
      self.IsSubmitWorking(false);
    }
  };

  onChange = (event) => {
    var errorName = [event.target.name] + "Form";

    this.setState({
      [event.target.name]: event.target.value,
      [errorName]: "",
    });
  };

  render() {
    //const classes = this.props.styleClass;
    const fixedHeightPaper = this.props.styleFixedHeight;
    const { username, email, org } = this.state;

    // const pError = passwordOne !== passwordTwo;
    // const pLenError = passwordOne.length <= 6 || passwordTwo.length <= 6;
    // const pHelper = pError
    //   ? "Those passwords didn't match. Try again."
    //   : pLenError
    //   ? "Use 6 or more characters with a mix of letters, numbers & symbols"
    //   : "";
    //var validEmailRegEx = /^[A-Z0-9_'%=+!`#~$*?^{}&|-]+([\.][A-Z0-9_'%=+!`#~$*?^{}&|-]+)*@[A-Z0-9-]+(\.[A-Z0-9-]+)+$/i;

    // var passwordRegEx = /^(?=.*[0-9])(?=.*[!@#$%^&*])[a-zA-Z0-9!@#$%^&*]{6,16}$/i;
    // var isInvalidPassword =
    //   passwordOne === "" ? true : passwordRegEx.test(passwordOne);
    // var pRegexHelper = !isInvalidPassword
    //   ? "Password should at least contain a number and a special character, it should also be at least 6 characters in length."
    //   : "";

    return (
      <React.Fragment>
        <Grid item xs={12}>
          <Paper className={fixedHeightPaper}>
            <Typography variant="h5" gutterBottom>
              Admins
            </Typography>
            <form onSubmit={this.onSubmit} autoComplete="off" noValidate>
              <input autoComplete="new-password" type="password" hidden />

              <Grid container spacing={2} alignItems="stretch">
                <Grid item xs={12}>
                  <Typography variant="subtitle1" gutterBottom>
                    Create New Admin
                  </Typography>
                </Grid>
                <Grid item xs={12} sm={6}>
                  <TextField
                    error={this.state.usernameForm === "" ? false : true}
                    helperText={this.state.usernameForm}
                    id="username"
                    name="username"
                    label="Name"
                    variant="outlined"
                    onChange={this.onChange}
                    type="text"
                    autoComplete="off"
                    value={username}
                    readOnly
                    fullWidth
                  // onFocus="this.removeAttribute('readonly');"
                  />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <TextField
                    error={this.state.orgForm === "" ? false : true}
                    helperText={this.state.orgForm}
                    id="org"
                    name="org"
                    label="Organization"
                    variant="outlined"
                    onChange={this.onChange}
                    type="text"
                    value={org}
                    fullWidth
                  />
                </Grid>
                <Grid item xs={12} sm={12}>
                  <TextField
                    error={this.state.emailForm === "" ? false : true}
                    helperText={this.state.emailForm}
                    id="email"
                    name="email"
                    label="Email"
                    variant="outlined"
                    onChange={this.onChange}
                    type="email"
                    autoComplete="off"
                    value={email}
                    //required
                    readOnly
                    fullWidth
                  // onFocus="this.removeAttribute('readonly');"
                  />
                </Grid>
                {/* <Grid item xs={12} sm={6}></Grid> */}
                <Grid item xs={12}>
                  <center>
                    <Button
                      variant="contained"
                      color="primary"
                      type="submit"
                      size="large"
                      style={{ height: 50, width: 220 }}
                      disabled={this.state.isSubmitted}
                    >
                      Submit
                    </Button>
                  </center>
                </Grid>
              </Grid>
            </form>
          </Paper>
        </Grid>
        <Grid item xs={12}>
          {this.state.refresh === true ? (
            <center>
              <CircularProgress />
            </center>
          ) : (
            <CreateTable
              firebase={this.props.firebase}
              header="Admins"
              errorHandler={this.errorHandler}
            />
          )}
        </Grid>
        {this.state.errorText !== "" ? (
          <Snackbar
            severity={this.state.errorType}
            message={this.state.errorText}
            onClose={() => {
              this.setState({ errorText: "" });
            }}
          />
        ) : (
          <div />
        )}
      </React.Fragment>
    );
  }
}

// export default UserList;
const condition = (authUser) =>
  authUser && authUser.roles.includes(ROLES.SUPERADMIN);
export default compose(withAuthorization(condition))(UserList);