import React from "react";
import MUIDataTable from "mui-datatables";
import { MuiThemeProvider, createMuiTheme } from "@material-ui/core/styles";
import { CircularProgress } from "@material-ui/core";
import ActiveToggleFirebase from "../Common/ActiveToggle";
import DeleteButtonFirebase from "../Common/DeleteButton";
import FirebaseInfoModal from "./InfoModal";
import { Grid } from "@material-ui/core";
import { FetchUserMobileAPI } from "../../helpers/DashboardHelper.js";
var moment = require('moment'); // require
 moment().format("R")

let _isMounted = false;

function CreateTableData(
  result,
  uid,
  userName,
  emailAddress,
  phoneNumber,
  lastLogin,
  lastLogOut,
  status,
  country,
  province,
  district,
  modulesPassed,
  modulesCompleted,
  landSize,
  averageProduction,
  averageHarvest,
  associateId,
  farmerGroup,
  errorHandler,
  refreshHandler,
  em,
  isFarmer,
  gender
) {
  let temp = [];
  temp.push(CheckForNull(uid));
  temp.push(CheckForNull(userName));
  temp.push(CheckForNull(emailAddress));
  temp.push(CheckForNull(phoneNumber));

  temp.push(CheckForNull(moment(lastLogin).format('YYYY/MM/DD')));
  temp.push(CheckForNull(moment(lastLogOut).format('YYYY/MM/DD')));
  temp.push(CheckForNull(country));
  temp.push(CheckForNull(province));
  temp.push(CheckForNull(district));
  temp.push(CheckForNull(isFarmer));
  temp.push(CheckForNull(farmerGroup));
  temp.push(CheckForNull(landSize));
  temp.push(CheckForNull(gender));

  // moment().format("R")
  // console.log(lastLogin + " " + moment(lastLogin).format('YYYY/MM/DD'));

  //phase2 edit, interchange these 2 values

  temp.push(CheckForNull(modulesCompleted));
  temp.push(CheckForNull(modulesPassed));

  var baseProduction = (parseInt(averageProduction) / parseInt(averageHarvest)) * 365;
  var baseProductionPerLandSize = baseProduction / parseInt(landSize);

  var but = (
    <React.Fragment>
      <Grid container spacing={0}>
        <Grid item xs={12}>
          <center>
            <FirebaseInfoModal
              result={result}
              uid={uid}
              c={CheckForNull(country)}
              pv={CheckForNull(province)}
              ls={CheckForNull(landSize)}
              ap={CheckForNull(averageProduction)}
              ah={CheckForNull(averageHarvest)}
              bp={
                isNaN(baseProduction)
                  ? "Nil"
                  : Math.round((baseProduction + Number.EPSILON) * 100) / 100
              }
              bpl={
                isNaN(baseProductionPerLandSize)
                  ? "Nil"
                  : Math.round((baseProductionPerLandSize + Number.EPSILON) * 100) / 100
              }
              fa={CheckForNull(associateId)}
              fg={CheckForNull(farmerGroup)}
              errorHandler={errorHandler}
              refreshHandler={refreshHandler}
            />
          </center>
        </Grid>
      </Grid>
    </React.Fragment>
  );
  temp.push(but);

  but = (
    <React.Fragment>
      <Grid container spacing={0}>
        <Grid item xs={12}>
          <center>
            <ActiveToggleFirebase
              uid={uid}
              status={status}
              dbType="mobile_users/"
              errorHandler={errorHandler}
              refreshHandler={refreshHandler}
            />
          </center>
        </Grid>
      </Grid>
    </React.Fragment>
  );
  temp.push(but);

  but = (
    <React.Fragment>
      <Grid container spacing={0}>
        <Grid item xs={12}>
          <center>
            <DeleteButtonFirebase
              type="Mobile"
              uid={uid}
              errorHandler={errorHandler}
              refreshHandler={refreshHandler}
              email={em}
            />
          </center>
        </Grid>
      </Grid>
    </React.Fragment>
  );
  temp.push(but);
  return temp;
}


function CheckForNull(val) {
  if (val === "" || val == null || val === "NaN" || val === "undefined") return "Nil";
  else return val;
}

const loadingData = [
  [
    "",
    "Loading Data",
    <CircularProgress />,
    <CircularProgress />,
    <CircularProgress />,
    <CircularProgress />,
    <CircularProgress />,
    <CircularProgress />,
    <CircularProgress />,
    <CircularProgress />,
    <CircularProgress />,
    <CircularProgress />,
    <CircularProgress />,
    <CircularProgress />,
    <CircularProgress />,
    <CircularProgress />,
    <CircularProgress />,
    <CircularProgress />,
    <CircularProgress />,
  ],
];

// phase2 - add logout gender group landsize

const columns = [
  {
    name: "UID",
    options: {
      filter: false,
      display: false,
      sort: false,
    },
  },
  {
    name: "Name",
    options: {
      filter: false,
      sort: false,
    },
  },
  {
    name: "Email",
    options: {
      filter: false,
      sort: false,
    },
  },
  {
    name: "Phone number",
    options: {
      filter: false,
      sort: false,
    },
  },

  {
    name: "Last Login",
    options: {
      filter: false,
      sort: true,
    },
  },
  {
    name: "Last LogOut",
    options: {
      filter: false,
      sort: true,
    },
  },
  {
    name: "Country",
    options: {
      filter: true,
      sort: true,
    },
  },
  {
    name: "Province",
    options: {
      filter: true,
      sort: true,
    },
  },
  //adding here extra details
  {
    name: "District",
    options: {
      filter: true,
      sort: true,
    },
  },
  {
    name: "Farmer/Non-Farmer",
    options: {
      filter: false,
      display: false,
    },
  },
  {
    name: "Farmer Group",
    options: {
      filter: false,
      display: false,
    },
  },
  {
    name: "Land Size",
    options: {
      filter: false,
      display: false,
    },
  },
  {
    name: "Gender",
    options: {
      filter: false,
      display: false,
    },
  },
  // 4 elements added
  {
    name: "Modules Completed",
    options: {
      filter: true,
      sort: true,
    },
  },
  {
    name: "Modules Passed",
    options: {
      filter: true,
      sort: true,
    },
  },
  {
    name: "More Details",
    options: {
      sort: false,
      filter: false,
    },
  },
  {
    name: "Status",
    options: {
      sort: false,
      filter: false,
    },
  },
  {
    name: "Delete",
    options: {
      sort: false,
      filter: false,
    },
  },
];

const options = {
  customToolbarSelect: () => { },
  filterType: "dropdown",
  print: false,
  download: true,
  viewColumns: false,
  selectableRows: false
};

const theme = createMuiTheme({
  overrides: {
    MUIDataTableSelectCell: {
      expandDisabled: {
        // Soft hide the button.
        visibility: "show",
      },
    },
  },
});

let listOfUsers = [];
class CreateTable extends React.Component {
  constructor(props) {
    super(props);
    this.props = props;
    this.state = {
      data: [],
      passData: [],
      rowsExpanded: [],
      refresh: false,
    };
    this.errorHandler = this.props.errorHandler;
    this.refreshHandler = this.RefreshHandler.bind(this);

  }
  _isMounted = false;

  RefreshHandler(ref) {
    if (ref) {
      this.setState({
        refresh: false,
        tableData: [],
      });
      this.componentDidMount();
    }
  }

  async FetchDataMobile() {
    let tmpData = [];
    let tPassData = [];
    let result = null;
    if (_isMounted) {
      FetchUserMobileAPI().then(response => {
        listOfUsers = [];
        listOfUsers = JSON.parse(response.data.userList); 
        result = listOfUsers;
        if (_isMounted) {
          for (var key in result) {
            let lastLogin = "";
            if (result[key]["lastLogin"]) {
              lastLogin = new Date(result[key]["lastLogin"]).toDateString();
            }
            let _key = key;

            var count = 0; // to ignore first result - Bin Wei structure ( result[0] is pre which is not to be stored or display anymore)
            var r = [];
            for (var modID in result[_key]["moduleResults"]) {
              var mod = result[_key]["moduleResults"][modID];
              for (var rID in mod["results"]) {
                count++;

                if (count !== 1) {
                  r.push(mod["results"][rID]["score"]);
                }
              }
            }
            var max = parseInt(r[0]);
            for (var l = 1; l < r.length; l++) {
              if (parseInt(r[l]) > max) {
                max = parseInt(r[l]);
              }
            }

            let modulesPassed = 0;
            let modulesCompleted = 0;
            if (
              result[_key]["moduleResults"][0]["finishPostAssessment"] === true
            ) {
              modulesPassed += 1;
            }
            if (result[_key]["moduleResults"][0]["completedModule"] === true) {
              modulesCompleted += 1;
            }

            if (result[_key]["moduleResults"][1] != null) {
              if (
                result[_key]["moduleResults"][1]["finishPostAssessment"] ===
                true
              ) {
                modulesPassed += 1;
              }
              if (
                result[_key]["moduleResults"][1]["completedModule"] === true
              ) {
                modulesCompleted += 1;
              }
            }
            

            // phase2 - login split, remove beofre 2021


            var login = lastLogin.split(' ');
            var lastLogout = (CheckForNull(result[key]["userData"]["lastLogout"]) === "Nil") ? lastLogin : result[key]["userData"]["lastLogout"];
            var province = result[key]["userData"]["province"];
            var district = (CheckForNull(result[key]["userData"]["district"]) === "Nil") ? province : result[key]["userData"]["district"];
            if(login[3] === "2021" || login[3]=== "2020")
            {
              ;
            }
            else
            {
              tmpData.push(
                CreateTableData(
                  result,
                  result[key]["_id"],
                  result[key]["userData"]["userName"],
                  result[key]["userData"]["emailAddress"],
                  result[key]["userData"]["phoneNumber"],
                  lastLogin,
                  lastLogout,
                  result[key]["isActive"],
                  result[key]["userData"]["country"],
                  province,
                  district,
                  modulesPassed,
                  modulesCompleted,
                  result[key]["userData"]["landSize"],
                  result[key]["userData"]["averageProduction"],
                  result[key]["userData"]["averageHarvest"],
                  result[key]["userData"]["associateId"],
                  result[key]["userData"]["farmerGroup"],
                  this.errorHandler,
                  this.refreshHandler,
                  result[key]["userData"]["emailAddress"],
                  result[key]["userData"]["isFarmer"],
                  result[key]["userData"]["gender"],

                )
              );
            }
          }
          if (_isMounted) {
            this.setState({
              data: tmpData,
              passData: tPassData,
            });
          }
        }
        else {
        }
      });
    }
  }


  async componentDidMount() {
    // console.log("mounted");
    _isMounted = true;
    if (_isMounted) this.FetchDataMobile();
  }

  componentWillUnmount() {
    _isMounted = false;
    this.setState({
      data: [],
      passData: [],
    });
  }



  render() {
    return (
      <React.Fragment>
        {this.state.data.length <= 0 ? (
          <center>
            <CircularProgress />
            <MuiThemeProvider theme={theme}>
              <MUIDataTable
                title={this.props.TableHeader}
                data={loadingData}
                columns={columns}
                options={options}
              />
            </MuiThemeProvider>
          </center>
        ) : (
          <MuiThemeProvider theme={theme}>
            <MUIDataTable
              title={this.props.TableHeader}
              data={this.state.data}
              columns={columns}
              options={options}
            />
          </MuiThemeProvider>
        )}
      </React.Fragment>
    );
  }
}

export default (CreateTable);