import React from 'react';
import { Typography, Paper, Grid } from '@material-ui/core';
import { Skeleton } from '@material-ui/lab';




export default function InfoCard(props) {
  return (
    <React.Fragment>
      <Paper className={props.styleClass} style={{ height: "211px", width: "auto" }} elevation={2}>
        <center>
          {(props.val === -1 || props.val === "-1%") ? (
            <React.Fragment>
              <Skeleton animation="wave" variant="circle" width={50} height={50} />
              <Skeleton animation="wave" variant="text" width="80%" height={30} />
            </React.Fragment>
          ) : (
            props.type === undefined ? (<React.Fragment>
              <Typography component="p" variant="h4" gutterBottom>
                {props.val}
              </Typography>
              <Typography variant="subtitle2" color="textSecondary" gutterBottom>
                {props.title.toUpperCase()}
              </Typography>
              <hr></hr>
              <Grid container>
                <Grid item lg={6} sm={12}>
                  <Typography variant="subtitle1" color="primary" align="left">
                    Male :  {props.male}
                  </Typography>
                </Grid>
                <Grid item lg={6} sm={12}>
                  <Typography variant="subtitle1" color="primary" align="right" >
                    Female : {props.female}
                  </Typography>
                </Grid>
              </Grid>
            </React.Fragment>) : (
              <React.Fragment>
                <Typography component="p" variant="h4" gutterBottom>
                  {props.title.toUpperCase()}
                </Typography>

                <Grid container>
              
                  {props.type2Val.length > -1 ? (
                    props.type2Val.map((pro, i) => {
                      return (
                        <React.Fragment>
                          <Grid item lg={8} sm={8} key={pro.name}>
                            <Typography variant="subtitle1" color="primary" align="left">
                              {pro.name}
                            </Typography>
                          </Grid>
                          <Grid item lg={4} sm={4} key={i}>
                            <Typography variant="subtitle1" color="primary" align="right">
                              :  {pro.count}
                            </Typography>
                          </Grid>
                        </React.Fragment>
                      )
                      // console.log(pro.name)

                    })

                  ) : (
                    <React.Fragment>
                      <Grid item lg={8} sm={12} key="nodata">
                        <Typography variant="subtitle1" color="primary" align="left">
                          Not sufficient data
                        </Typography>
                      </Grid>

                    </React.Fragment>
                  )
                  }
                </Grid>

              </React.Fragment>
            )

          )}
        </center>
      </Paper>
    </React.Fragment >
  );
}