import React from "react";
import AuthUserContext from "./context";

import * as StateManagement from "../Session/stateManagement";


let userData = null;
const withAuthentication = (Component) => {
  class WithAuthentication extends React.Component {
    constructor(props) {
      super(props);

      this.state = {
        authUser: null,
      };
      userData = JSON.parse(localStorage.getItem("authUser"));
      if (userData != null) {
        if (StateManagement.CheckForTimeOut(userData.time)) {
          ;
        }
        else {
          this.state = {
            authUser: userData.authUser,
          };
        }
      
      }
      else {

        this.state = { authUser: null };
        localStorage.clear();
   
      }
    }

    SetLocalStorage(authUser) {
      var now = new Date().getTime();
      const userData = {
        time: now,
        authUser: authUser,
      };
      localStorage.setItem("authUser", JSON.stringify(userData));
    }

    componentDidMount() {
    }



    componentWillUnmount() {

    }

    render() {
      return (
        <AuthUserContext.Provider value={this.state.authUser}>
          <Component {...this.props} />
        </AuthUserContext.Provider>
      );
    }
  }

  return (WithAuthentication);
};

export default withAuthentication;
