import ActiveToggleFirebase from "../Common/ActiveToggle";
import DeleteButtonFirebase from "../Common/DeleteButton";
import DownloadCert from "../Common/DownloadCert";
import FirebaseInfoModal from "./InfoModal";
import { columns, theme, options } from "./Constants";
import React, { useState, useEffect } from "react";
import { CircularProgress, MuiThemeProvider } from "@material-ui/core";
import { FetchUserMobileAPI } from "../../helpers/DashboardHelper";
import MUIDataTable from "mui-datatables";
import moment from "moment/moment";



function CreateTableData(
    result,
    uid,
    userName,
    emailAddress,
    phoneNumber,
    lastSignIn,
    lastLogOut,
    status,
    country,
    province,
    district,
    modulesPassed,
    modulesCompleted,
    landSize,
    averageProduction,
    averageHarvest,
    associateId,
    farmerGroup,
    errorHandler,
    refreshHandler,
    em,
    isFarmer,
    gender
) {
    let temp = [];
    temp.push(CheckForNull(uid));
    temp.push(CheckForNull(userName));

    var baseProduction, baseProductionPerLandSize; // Define variables here

    if (!isNaN(parseInt(averageProduction)) && !isNaN(parseInt(averageHarvest))) {
        baseProduction = (parseInt(averageProduction) / parseInt(averageHarvest)) * 365;
        baseProductionPerLandSize = baseProduction / parseInt(landSize);
    } else {
        baseProduction = "Nil";
        baseProductionPerLandSize = "Nil";
    }

    const createInfoModal = () => (
        <FirebaseInfoModal
            result={result}
            uid={uid}
            c={CheckForNull(country)}
            pv={CheckForNull(province)}
            ls={CheckForNull(landSize)}
            ap={CheckForNull(averageProduction)}
            ah={CheckForNull(averageHarvest)}
            bp={
                isNaN(baseProduction)
                    ? "Nil"
                    : Math.round((baseProduction + Number.EPSILON) * 100) / 100
            }
            bpl={
                isNaN(baseProductionPerLandSize)
                    ? "Nil"
                    : Math.round((baseProductionPerLandSize + Number.EPSILON) * 100) / 100
            }
            fa={CheckForNull(associateId)}
            fg={CheckForNull(farmerGroup)}
            errorHandler={errorHandler}
            refreshHandler={refreshHandler}
        />
    );

    const createActiveToggle = () => (
        <ActiveToggleFirebase
            uid={uid}
            status={status}
            dbType="mobile_users/"
            errorHandler={errorHandler}
            refreshHandler={refreshHandler}
        />
    );

    const createDeleteButton = () => (
        <DeleteButtonFirebase
            type="Mobile"
            uid={uid}
            errorHandler={errorHandler}
            refreshHandler={refreshHandler}
            email={em}
        />
    );

    const createDownloadCert = () => (
        <DownloadCert name={userName} module="Pruning of COCOA" />
    );

    temp.push(CheckForNull(modulesCompleted));
    temp.push(CheckForNull(modulesPassed));
    temp.push(createInfoModal());
    temp.push(createActiveToggle());
    temp.push(createDeleteButton());
    temp.push(createDownloadCert());

    return temp;
}


function CheckForNull(val) {
    if (val === "" || val == null || val === "NaN" || val === "undefined") return "Nil";
    else return val;
}

const loadingData = [
    [
        "",
        "Loading Data",
        <CircularProgress />,
        <CircularProgress />,
        <CircularProgress />,

    ],
];

export default function CreateTable(props) {
    const { errorHandler } = props;
    const [data, setData] = useState([]);
    const [passData, setPassData] = useState([]);
    const [rowsExpanded, setRowsExpanded] = useState([]);
    const [refresh, setRefresh] = useState(false);



    const RefreshHandler = (ref) => {
        if (ref) {
            setRefresh(false);
            setData([]);
            setRowsExpanded([]);
            setPassData([]);
            FetchDataMobile();
        }
    };

    useEffect(() => {
        FetchDataMobile();
    }, []);

    async function FetchDataMobile() {
        let tmpData = [];
        let tPassData = [];
        let result = null;

        async function fetchUserData() {
    
            const updatedResponse = await FetchUserMobileAPI();
            return updatedResponse.userList;
        }

        function formatLastLogin(lastLogin) {

            return lastLogin ? moment(lastLogin).format("ddd MMM DD YYYY") : "";
        }


        function getModulesData(key, result) {
            let r = [];
            try {
                const moduleResults = result[key].moduleResults;

                for (const mod of Object.values(moduleResults)) {
                    for (const res of mod.results) {
                        if (res.score !== '') {
                            r.push(parseInt(res.score));
                        }
                    }
                }

                const modulesPassed = r.filter(score => score > 0).length;
                const modulesCompleted = r.filter(score => score === 100).length;

                return { modulesPassed, modulesCompleted };
            } catch (e) {
                return { modulesPassed: 0, modulesCompleted: 0 };
            }
        }

        function getDistrict(province, district) {
            return CheckForNull(district) === "Nil" ? province : district;
        }

        async function createTableData(result, key) {

            let { modulesPassed, modulesCompleted } = getModulesData(key, result);

            let lastLogin = formatLastLogin(result[key]["lastLogin"]);
            let province = result[key]["userData"]["province"];
            let district = getDistrict(province, result[key]["userData"]["district"]);
            let lastLogout =
                CheckForNull(result[key]["userData"]["lastLogout"]) === "Nil"
                    ? lastLogin
                    : result[key]["userData"]["lastLogout"];

            if (
                lastLogin.split(" ")[3] !== "2021" &&
                lastLogin.split(" ")[3] !== "2022" &&

                modulesPassed >= 1
            ) {
                let tableData = CreateTableData(
                    result,
                    result[key]["_id"],
                    result[key]["userData"]["userName"],
                    result[key]["userData"]["emailAddress"],
                    result[key]["userData"]["phoneNumber"],
                    lastLogin,
                    lastLogout,
                    result[key]["isActive"],
                    result[key]["userData"]["country"],
                    province,
                    district,
                    modulesPassed,
                    modulesCompleted,
                    result[key]["userData"]["landSize"],
                    result[key]["userData"]["averageProduction"],
                    result[key]["userData"]["averageHarvest"],
                    result[key]["userData"]["associateId"],
                    result[key]["userData"]["farmerGroup"],
                    errorHandler,
                    RefreshHandler,
                    result[key]["userData"]["emailAddress"],
                    result[key]["userData"]["isFarmer"],
                    result[key]["userData"]["gender"]
                );
                tmpData.push(tableData);
            }
        }

        let listOfUsers = await fetchUserData();
        result = listOfUsers;
        for (var key in result) {
            await createTableData(result, key);
        }
        setData(tmpData);
        setPassData(tmpData);

    }

    return (
        <React.Fragment>
            {data.length <= 0 ? (
                <center>
                    <MuiThemeProvider theme={theme}>
                        <MUIDataTable
                            title={props.TableHeader}
                            data={loadingData}
                            columns={columns}
                            options={options}
                        />
                    </MuiThemeProvider>
                </center>
            ) : (
                <MuiThemeProvider theme={theme}>

                    <MUIDataTable
                        title={props.TableHeader}
                        data={data}
                        columns={columns}
                        options={options}
                    />
                </MuiThemeProvider>
            )}
        </React.Fragment>
    );

}