import React from "react";
import clsx from "clsx";
import { useStyles } from '../Common/Styles';
import UserList from "./UserList";
// import { compose } from "recompose";
import { withAuthorization } from "../Session";
import * as ROLES from "../../constants/roles";

function DashboardViewer() {
  const classes = useStyles();
  const fixedHeightPaperLarge = clsx(classes.paper, classes.fixedHeightLarge);
  return (
    <UserList
      styleClass={classes}
      styleFixedHeight={fixedHeightPaperLarge}
    />
  );
}


const condition = (authUser) =>
  authUser &&
  (authUser.roles.includes(ROLES.ADMIN) ||
    authUser.roles.includes(ROLES.SUPERADMIN));
export default withAuthorization(condition)(DashboardViewer);
// export default DashboardViewer;