import { BASE_URL } from '../constants/constants';
import * as ROLES from "../constants/roles";
import { CheckForTimeOut } from "../components/Session/stateManagement";
require('dotenv').config();
const axios = require('axios');



export async function DoLogin(payload) {

    return await axios.post(BASE_URL + "/login", payload)
        .then(response => {
            if (response.status === 200)
                return response.data;
            else
                return null;
        }).catch(error => {
            return error.response;
        });
}

export async function LogoutWebUserKeycloak() {
    const refreshToken = localStorage.getItem("refreshToken");
    const accessToken = localStorage.getItem('auth_token');
    let payload = {
        refreshToken: refreshToken
    };
    return await axios.post(BASE_URL + "/logoutkeycloak", payload, {
        headers: {
            'Authorization': `Bearer ${accessToken}`
        }
    })
        .then(response => {           
            return response;
        }).catch(error => {
            console.log(error);
            return error.response;
        });
}

export async function RegisterWebUser(payload) {
    const accessToken = localStorage.getItem('auth_token');
    //console.log(accessToken);
    var userData = JSON.parse(localStorage.getItem("authUser"));
    if (CheckForTimeOut(userData.time)) {
        ;
    }
    else {

        return await axios.post(BASE_URL + "/register", payload, {
            headers: {
                'Authorization': `Bearer ${accessToken}`
            }
        })
            .then(function (response) {
                //console.log(response);
                return response;
            })
            .catch(function (error) {
                console.log(error);
            });
    }
}

export async function RegisterWebUserDashboard(payload) {
    const accessToken = localStorage.getItem('auth_token');
    //console.log(accessToken);
    var userData = JSON.parse(localStorage.getItem("authUser"));
    if (CheckForTimeOut(userData.time)) {
        ;
    }
    else {

        return await axios.post(BASE_URL + "/register", payload, {
            headers: {
                'Authorization': `Bearer ${accessToken}`
            }
        })
            .then(function (response) {
                //console.log(response);
                return response;
            })
            .catch(function (error) {
                if (error.response.status === 406) {
                    let res = {};
                    let userRole = error.response.data.userRole;

                    if (userRole.indexOf(ROLES.SUPERADMIN) > -1) {
                        res = {
                            data: {
                                success: false,
                                message: "User Already SuperAdmin !"
                            }
                        };
                        return res;
                    }

                    if (userRole.indexOf(ROLES.ADMIN) > -1) {
                        res = {
                            data: {
                                success: false,
                                message: "User Already Admin !"
                            }
                        };
                        return res;
                    }

                    if (userRole.indexOf("NULL") > -1) {
                        res = {
                            data: {
                                success: false,
                                message: "User Already Viewer !"
                            }
                        };
                        return res;
                    }
                }
            });
    }
}

