import React from "react";
import { Link } from "react-router-dom";
import {
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  ListSubheader,
} from "@material-ui/core";
import {
  Dashboard,
  Info,
  People,
  SupervisorAccount,
  Visibility,
  Assignment,
  PermMedia,
  Public,
} from "@material-ui/icons";
import theme from "./theme";
import * as ROUTES from "../../constants/routes";

const styles = {
  listStyle: {
    textDecoration: "none",
    color: theme.palette.primary.contrastText,
  },
};

export function NavBarList(props) {
  if (props.type === "superadmin") {
    return (
      <div>
        <List
          subheader={
            <ListSubheader style={{ align: "left" }}>
              {props.open ? "Reports" : ""}
            </ListSubheader>
          }
        >
          <Link style={styles.listStyle} to={ROUTES.HOME}>
            <ListItem button>
              <ListItemIcon style={styles.listStyle}>
                <Dashboard />
              </ListItemIcon>
              <ListItemText primary="Dashboard" />
            </ListItem>
          </Link>
        </List>

        <List subheader={<ListSubheader>
          {props.open ? "Getting Started" : ""}
          </ListSubheader>}>
          <Link style={styles.listStyle} to={ROUTES.ECOCOAABOUT}>
            <ListItem button>
              <ListItemIcon style={styles.listStyle}>
                <Info />
              </ListItemIcon>
              <ListItemText primary="About e-cocoa" />
            </ListItem>
          </Link>
        </List>

        <List subheader={<ListSubheader>
          {props.open ? "Management" : ""}
          </ListSubheader>}>
          <Link style={styles.listStyle} to={ROUTES.POSTASSESMENT}>
            <ListItem button>
              <ListItemIcon style={styles.listStyle}>
                <Assignment />
              </ListItemIcon>
              <ListItemText primary="Questions" />
            </ListItem>
          </Link>

          <Link style={styles.listStyle} to={ROUTES.APPUSERS}>
            <ListItem button>
              <ListItemIcon style={styles.listStyle}>
                <Public />
              </ListItemIcon>
              <ListItemText primary="App Users" />
            </ListItem>
          </Link>

          <Link style={styles.listStyle} to={ROUTES.DASHBOARDVIEWER}>
            <ListItem button>
              <ListItemIcon style={styles.listStyle}>
                <Visibility />
              </ListItemIcon>
              <ListItemText primary="Dashboard Viewers" />
            </ListItem>
          </Link>

          <Link style={styles.listStyle} to={ROUTES.CERTIFICATE}>
            <ListItem button>
              <ListItemIcon style={styles.listStyle}>
                <PermMedia />
              </ListItemIcon>
              <ListItemText primary="User Certificates" />
            </ListItem>
          </Link>

          <Link style={styles.listStyle} to={ROUTES.ADMIN}>
            <ListItem button>
              <ListItemIcon style={styles.listStyle}>
                <SupervisorAccount />
              </ListItemIcon>
              <ListItemText primary="Admins" />
            </ListItem>
          </Link>


        </List>
      </div>
    );
  }
  else if (props.type === "admin") {
    return (
      <div>
        <List
          subheader={
            <ListSubheader style={{ align: "left" }}>
              {props.open ? "Reports" : ""}
            </ListSubheader>
          }
        >
          <Link style={styles.listStyle} to={ROUTES.HOME}>
            <ListItem button>
              <ListItemIcon style={styles.listStyle}>
                <Dashboard />
              </ListItemIcon>
              <ListItemText primary="Dashboard" />
            </ListItem>
          </Link>
        </List>

        <List subheader={<ListSubheader>
          {props.open ? "Getting Started" : ""}
          </ListSubheader>}>
          <Link style={styles.listStyle} to={ROUTES.ECOCOAABOUT}>
            <ListItem button>
              <ListItemIcon style={styles.listStyle}>
                <Info />
              </ListItemIcon>
              <ListItemText primary="About e-cocoa" />
            </ListItem>
          </Link>
        </List>

        <List subheader={<ListSubheader>
          {props.open ? "Management" : ""}
          </ListSubheader>}>
          <Link style={styles.listStyle} to={ROUTES.POSTASSESMENT}>
            <ListItem button>
              <ListItemIcon style={styles.listStyle}>
                <Assignment />
              </ListItemIcon>
              <ListItemText primary="Questions" />
            </ListItem>
          </Link>

          <Link style={styles.listStyle} to={ROUTES.APPUSERS}>
            <ListItem button>
              <ListItemIcon style={styles.listStyle}>
                <People />
              </ListItemIcon>
              <ListItemText primary="App Users" />
            </ListItem>
          </Link>

          <Link style={styles.listStyle} to={ROUTES.DASHBOARDVIEWER}>
            <ListItem button>
              <ListItemIcon style={styles.listStyle}>
                <Visibility />
              </ListItemIcon>
              <ListItemText primary="Dashboard Viewers" />
            </ListItem>
          </Link>

          {/* <Link style={styles.listStyle} to={ROUTES.ADMIN}>
            <ListItem button>
              <ListItemIcon style={styles.listStyle}>
                <SupervisorAccount />
              </ListItemIcon>
              <ListItemText primary="Admins" />
            </ListItem>
          </Link> */}
        </List>
      </div>
    );
  } else {
    return (
      <div>
        <List subheader={<ListSubheader>
          {props.open ? "Reports" : ""}

          </ListSubheader>}>
          <Link style={styles.listStyle} to={ROUTES.HOME}>
            <ListItem button>
              <ListItemIcon style={styles.listStyle}>
                <Dashboard />
              </ListItemIcon>
              <ListItemText primary="Dashboard" />
            </ListItem>
          </Link>
        </List>

        <List subheader={<ListSubheader>
          {props.open ? "Getting Started" : ""}
          
          </ListSubheader>}>
          <Link style={styles.listStyle} to={ROUTES.ECOCOAABOUT}>
            <ListItem button>
              <ListItemIcon style={styles.listStyle}>
                <Info />
              </ListItemIcon>
              <ListItemText primary="About e-cocoa" />
            </ListItem>
          </Link>
        </List>
      </div>
    );
  }
}

