import React from "react";
//import { Link } from "react-router-dom";
// import { withFirebase } from "../Firebase";
import { TextField, Button } from "@material-ui/core";
//import Modal from "@material-ui/core/Modal";
import Snackbar from "../Common/Snackbar";
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import * as ResetPasswordHelper from "../../helpers/ResetPasswordHelper";


function PasswordForgetPage(props) {
  const [open, setOpen] = React.useState(false);
  const [emailF, setEmail] = React.useState("");
  const [eError, setEmailError] = React.useState("");
  const [submitError, setSubmitError] = React.useState(null);
  const [sev, setSeverity] = React.useState("error");

  const handleOpen = () => {
    setSubmitError(null);
    setEmail("")
    setEmailError("")
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const OnSubmitModal = (event) => {
    setEmailError(emailF === "" ? "Please enter an Email." : "");
    if (!isInvalidEmail || eError !== "") {
      return;
    } else {
      let payload = {
        email: emailF
      };
      ResetPasswordHelper.SendResetPasswordMail(payload).then(response => {
        if (response) {
          if (response.status === 200) {
            setSeverity("success");
            setSubmitError({
              message: "Email successfully sent!"
            });
            handleClose();
          }
          else {
            setSeverity("error");
            setSubmitError(response);
          }
        }
      });
   
    }
    event.preventDefault();
  };

  const onChangeModal = (event) => {
    setEmail(event.target.value);
  };

  var validEmailRegEx = /^[A-Z0-9_'%=+!`#~$*?^{}&|-]+([.][A-Z0-9_'%=+!`#~$*?^{}&|-]+)*@[A-Z0-9-]+(\.[A-Z0-9-]+)+$/i;
  var isInvalidEmail = emailF === "" ? true : validEmailRegEx.test(emailF);
  var eHelper = !isInvalidEmail ? "The Email format is invalid." : "";

  const body = (
    <div>
      <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
        <DialogTitle id="form-dialog-title">Reset Password</DialogTitle>
        <DialogContent>
          <DialogContentText>
            Please enter your registered email address and we will email you instructions on how to reset your password.
          </DialogContentText>
          <TextField
            error={!isInvalidEmail || (eError === "" ? false : true)}
            helperText={eError === "" ? eHelper : eError}
            id="emailF"
            name="emailF"
            autoFocus
            margin="dense"
            label="Email Address"
            type="email"
            fullWidth
            value={emailF}
            placeholder=""
            onChange={onChangeModal}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="secondary">
            Cancel
          </Button>
          <Button onClick={OnSubmitModal} color="primary">
            Submit
          </Button>
        </DialogActions>
      </Dialog>
      {submitError && (
        <Snackbar
          severity={sev}
          message={submitError.message}
          onClose={() => {
            setSubmitError(null);
          }}
        />
      )}
    </div>


  );

  return (
    <React.Fragment>
      <Button
        color="primary"
        size="small"
        type="button"
        onClick={handleOpen}
        style={{ textDecoration: "none", color: "grey" }}
      >
        Forgot Password?
      </Button>
      {body}
    </React.Fragment>
  );
}

// const PasswordForgetLink = withFirebase(PasswordForgetPage);
const PasswordForgetLink = (PasswordForgetPage);

export default PasswordForgetPage;

export { PasswordForgetLink };
