import React from "react";
import MUIDataTable from "mui-datatables";
import { MuiThemeProvider, createMuiTheme } from "@material-ui/core/styles";
import { CircularProgress } from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
import ViewImage from "../Common/ViewImage";

var questionPruningJson = require("../Common/pruning_mod1.json");
var questionFertilizingJson = require("../Common/fertilizing_mod2.json");
//var questionMap = require("../Common/questions.json");

function createTableData(questionIsImage, questionImageURL, question, op1, op2, op3, op4, cA) {
  //console.log({question, op1, op2, op3, op4, cA});
  var temp = [];
  var currentQuestion = questionIsImage === true ? <React.Fragment>{question}<ViewImage imageURL={questionImageURL} /></React.Fragment> : question;
  // temp.push(questionIsImage);
  // temp.push(questionImageURL);
  temp.push(currentQuestion);
  temp.push(op1);
  temp.push(op2);
  temp.push(op3);
  temp.push(op4);
  temp.push(cA);

  return temp;
}

// function MapQuestion(qID) {
//   questionMap.questions.map((question) => {
//     var id = question.questionID.toString();
//     if (id.localCompare(qID) === 0) {
//       return question.questionText.toString();
//     }
//   });
// }

let quesData = [];
let quesDataHar = [];

class CreateTable extends React.Component {
  constructor(props) {
    super(props);
    this.props = props;
    this.state = { tableData: [], tableDataHar: [] };
  }

  componentDidMount() {
    questionPruningJson.questions.map((q) => {
      // console.log(q);
      quesData.push(
        createTableData(
          q.questionIsImage,
          q.questionImageURL,
          q.questionText,
          q.options[0].optionText,
          q.options[1].optionText,
          q.options[2] != null ? q.options[2].optionText : "NA",
          q.options[3] != null ? q.options[3].optionText : "NA",
          q.options[q.correctAnswer].optionText
        )
      );
      return null;
    });
    questionFertilizingJson.questions.map((q) => {
      quesDataHar.push(
        createTableData(
          q.questionIsImage,
          q.questionImageURL,
          q.questionText,
          q.options[0].optionText,
          q.options[1].optionText,
          q.options[2] != null ? q.options[2].optionText : "NA",
          q.options[3] != null ? q.options[3].optionText : "NA",
          q.options[q.correctAnswer].optionText
        )
      );
      return null;
    });
    this.setState({
      tableData: quesData,
      tableDataHar: quesDataHar,
    });
  }

  componentWillUnmount() {
    quesData = [];
    quesDataHar = [];
    this.setState({
      tableData: [],
      tableDataHar: [],
    });
  }

  render() {
    const columns = [
      {
        name: "Question",
        options: {
          filter: true,
          sort: true,
        },
      },
      {
        name: "Option A",
        options: {
          filter: true,
          sort: true,
        },
      },
      {
        name: "Option B",
        options: {
          filter: true,
          sort: true,
        },
      },
      {
        name: "Option C",
        options: {
          filter: true,
          sort: true,
        },
      },
      {
        name: "Option D",
        options: {
          filter: true,
          sort: true,
        },
      },
      {
        name: "Correct Answer",
        options: {
          filter: true,
          sort: true,
        },
      },
    ];

    const options = {
      customToolbarSelect: () => { },
      filterType: "dropdown",
  
      print: false,
      download: true,
      viewColumns: false,
      selectableRows: false
    };

    const theme = createMuiTheme({
      overrides: {
        MUIDataTableSelectCell: {
          expandDisabled: {
            // Soft hide the button.
            visibility: "show",
          },
        },
      },
    });

    return (
      <React.Fragment>
        <Grid container spacing={4}>
          <Grid item xs={12}>
            {this.state.tableData.length <= 0 ? (
              <center>
                <CircularProgress />
              </center>
            ) : (
              <MuiThemeProvider theme={theme}>
                <MUIDataTable
                  //title={this.props.TableHeader}
                  title="Pruning of Cocoa Post-Assesment Questions"
                  data={this.state.tableData}
                  columns={columns}
                  options={options}
                />
              </MuiThemeProvider>
            )}
          </Grid>
          <Grid item xs={12}>
            {this.state.tableDataHar.length <= 0 ? (
              <center>
                <CircularProgress />
              </center>
            ) : (
              <MuiThemeProvider theme={theme}>
                <MUIDataTable
                  //title={this.props.TableHeader}
                  title="Fertilizing of Cocoa Post-Assesment Questions"
                  data={this.state.tableDataHar}
                  columns={columns}
                  options={options}
                />
              </MuiThemeProvider>
            )}
          </Grid>
        </Grid>
      </React.Fragment>
    );
  }
}

export default CreateTable;
