import React from "react";
import Modal from "@material-ui/core/Modal";
// import { withFirebase } from "../Firebase";
import { Button, TextField, Grid } from "@material-ui/core";
import Typography from "@material-ui/core/Typography";
import { useStyles } from "../Common/Styles";
import EditIcon from "@material-ui/icons/Edit";
import { NameUpdaterContext } from "../App";
import { UpdateWebUser_Name_Org } from "../../helpers/UserHelper";

function getModalStyle() {
  const top = 50;
  const left = 50;

  return {
    top: `${top}%`,
    left: `${left}%`,
    transform: `translate(-${top}%, -${left}%)`,
  };
}

function EditModal(props) {
  const startUsername = props.uname;
  const startOrg = props.org;
  const uid = props.uid;
  const NameUpdater = React.useContext(NameUpdaterContext);
  // const [formState, formUpdate] = React.useState({
  //   username: "",
  //   org: "",
  // });
  let errorHandler = props.errorHandler;
  let refreshHandler = props.refreshHandler;

  const [saveBut, SetSaveStatus] = React.useState(false);
  const [username, SetUsername] = React.useState(null);
  const [org, SetOrg] = React.useState(null);
  const classes = useStyles();

  const [modalStyle] = React.useState(getModalStyle);
  const [open, setOpen] = React.useState(false);

  React.useEffect(() => {
    SetSaveStatus(username === startUsername && org === startOrg);
  }, [username, org]); // eslint-disable-line react-hooks/exhaustive-deps

  const handleOpen = () => {
    SetUsername(startUsername);
    SetOrg(startOrg);
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const OnSubmitModal = (event) => {
    event.preventDefault();
    let payload = {
      _id: uid,
      userName: username,
      org: org
    };

    UpdateWebUser_Name_Org(payload).then(response => {
      if (response.data.success) {
        handleClose();
        errorHandler("success", "Updated Information!");
        var user = JSON.parse(localStorage.getItem("authUser"));
        if (user.authUser.uid === uid) {
          NameUpdater(username);
        }
        refreshHandler(true);
      }
      else {
        errorHandler("error", response.data.message);
      }
    });
  };

  const onChangeModal = (event) => {
    event.target.name === "username"
      ? SetUsername(event.target.value)
      : SetOrg(event.target.value);
  };

  const body = (
    <div style={modalStyle} className={classes.paperModal}>
      <form onSubmit={OnSubmitModal}>
        <Grid
          container
          direction="row"
          justify="space-between"
          alignItems="center"
          spacing={2}
        >
          <Grid item xs={12}>
            <Typography
              component="h2"
              variant="h6"
              color="textPrimary"
              gutterBottom
            >
              <center>Edit Fields</center>
            </Typography>
          </Grid>
          <Grid item xs={6}>
            <TextField
              id="username"
              name="username"
              label="Username"
              variant="outlined"
              onChange={onChangeModal}
              type="text"
              autoComplete="off"
              //   defaultValue={props.uname}
              value={username}
              required
            />
          </Grid>

          <Grid item xs={6}>
            <TextField
              id="org"
              name="org"
              label="Organization"
              variant="outlined"
              onChange={onChangeModal}
              type="text"
              //   defaultValue={props.org}
              value={org}
              required
            />
          </Grid>
          <Grid item xs={6}>
            <center>
              <Button
                variant="contained"
                color="primary"
                size="large"
                type="submit"
                disabled={saveBut}
              >
                Save
              </Button>
            </center>
          </Grid>
          <Grid item xs={6}>
            <center>
              <Button
                variant="outlined"
                color="secondary"
                size="large"
                type="button"
                onClick={handleClose}
              >
                Cancel
              </Button>
            </center>
          </Grid>
        </Grid>
      </form>
    </div>
  );

  return (
    <div>
      <Button
        variant="outlined"
        color="default"
        size="small"
        type="button"
        onClick={handleOpen}
        startIcon={<EditIcon />}
      >
        Edit
      </Button>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="simple-modal-title"
        aria-describedby="simple-modal-description"
      >
        {body}
      </Modal>
    </div>
  );
}

//const FirebaseEditModal = withFirebase(EditModal);
const FirebaseEditModal = (EditModal);
export default FirebaseEditModal;
