import React from "react";
// import { withFirebase } from "../Firebase";
import ToggleOnIcon from "@material-ui/icons/ToggleOn";
import ToggleOffIcon from "@material-ui/icons/ToggleOff";
import { green } from "@material-ui/core/colors";
import { ToggleWebUser, ToggleMobilebUser } from "../../helpers/UserHelper";

class ActiveToggle extends React.Component {
  constructor(props) {
    super(props);

    this.errorHandler = this.props.errorHandler;
    this.refreshHandler = this.props.refreshHandler;
  }

  onSubmit = (uid, status, dbType) => {
    var self = this;
    var newStatus = status === "true" ? "false" : "true";
    let payload = {
      _id: uid,
      newStatus: newStatus
    };
    if (dbType === "web_users/") {
      ToggleWebUser(payload).then(response => {
        if (response.data.success) {
          self.errorHandler("success", "Updated!");
          self.refreshHandler(true);
        }
        else {
          self.errorHandler("error", response.data.message);
        }
      });
    }
    else {
      // console.log(uid);
      ToggleMobilebUser(payload).then(response => {
        // console.log(response);
        if (response.data.success) {
          self.errorHandler("success", "Updated!");
          self.refreshHandler(true);
        }
        else {
          self.errorHandler("error", response.data.message);
        }
      });
    }
  };

  render() {
    var active = (
      <ToggleOnIcon
        type="button"
        fontSize="large"
        onClick={() =>
          this.onSubmit(this.props.uid, this.props.status, this.props.dbType)
        }
        style={{ color: green[500] }}
      />
    );

    var inactive = (
      <ToggleOffIcon
        type="button"
        fontSize="large"
        onClick={() =>
          this.onSubmit(this.props.uid, this.props.status, this.props.dbType)
        }
        color="secondary"
      />
    );
    return <div>{this.props.status === "true" ? active : inactive}</div>;
  }
}

// const ActiveToggleFirebase = withFirebase(ActiveToggle);
const ActiveToggleFirebase = (ActiveToggle);

export default ActiveToggleFirebase;
